  /**
   * 根据分辨率计算获取约束和带宽
   * @param withAudio
   * @param withVideo
   * @param audioInput
   * @param videoInput
   * @param videoResolution
   * @returns {{audio: *, video: *}}
   */
  getConstraintsByResolution: function(withAudio, withVideo, audioInput, videoInput, videoResolution){
    var _this = this;
    var constraints = {
      audio: withAudio ? {} : false,
      video: withVideo ? {} : false
    };
    if(withVideo === true && videoResolution){
      var resolution = ms.getResolution();
      resolution.forEach(function(obj){
          if (obj.height.exact == videoResolution) {
              constraints.video = { width: obj.width, height: obj.height};
              config.videoBandwidth =  _this.getVideoBandwidthByResolution(videoResolution) ? _this.getVideoBandwidthByResolution(videoResolution) : obj.videoBandwidth;
              return;
          }
      });
    }
    if(withAudio === true){
      var aDevice =  audioInput ? audioInput : undefined;
      var cons = '{"optional":[{"sourceId":"'+aDevice+'"},{"echoCancellation":true},{"googEchoCancellation":true},{"googAutoGainControl":true},{"googNoiseSuppression":true},{"googHighpassFilter":true},{"googNoiseSuppression2":true},{"googEchoCancellation2":true},{"googAutoGainControl2":true},{"googTypingNoiseDetection":true}]}'
      var audioConstraints =  JSON.parse(cons)
      constraints.audio = audioConstraints;
    }
    if(withVideo === true){
      constraints.video.deviceId =  videoInput ? {exact: videoInput} : undefined;
      constraints.video.frameRate = { max: 30 , min: 20};
    }
    console.log("=========constraints====",constraints);
    return constraints;
  },

  /**
   *
   * @param resolution
   * @returns {*}
   */
  getVideoBandwidthByResolution: function(resolution){
    resolution = resolution.toString();
    var videoBandwidth = null;
    var rbwc = config.rbwc;
    if(!rbwc || !rbwc.length){
      return videoBandwidth;
    }
    for(var i = 0; i < rbwc.length; i++){
      var item = rbwc[i];
      if(item.height == resolution){
        videoBandwidth = item.bandwidth;
      }
    }
    return videoBandwidth;
  },

  /**
   * 浏览器是否支持localStorage
   *
   * @returns {boolean}
   */
  supportsLocalStorage: function () {
    try {
      return 'localStorage' in window && window.localStorage !== null;
    } catch (e) {
      console.log("localstorage is not supported");
      return false;
    }
  },

  /**
   * 生成唯一标识
   * @returns {*}
   */
  generateUniqueId: function () {
    function _p8() {
      return (Math.random().toString(16) + "000000000").substr(2, 8);
    }

    return _p8() + _p8() + _p8() + _p8();
  }
  ,

  /**
   * Checks whether the audio is muted or not.
   * @returns {boolean} true if audio is muted and false if not.
   */
  isAudioMuted: function () {
    var localAudio = connection.jingle.localAudio;
    for (var idx = 0; idx < localAudio.getAudioTracks().length; idx++) {
      if (localAudio.getAudioTracks()[idx].enabled === true)
        return false;
    }
    return true;
  }
  ,

  isVideoMuted: function () {
    var tracks = connection.jingle.localVideo.getVideoTracks();
    var mute = true;

    for (var i = 0; i < tracks.length; ++i) {
      if (tracks[i].enabled) {
        mute = false;
        break;
      }
    }
    return mute;
  },

  setAdmin: function (obj) {
    if (obj) {
      config.role = 'CRUO';
    } else {
      config.role = 'NONE';
    }
  }
  ,

  /**
   *  判断角色是否主持人
   * @returns {boolean}
   */
  isAdmin: function () {
    if (config.role == 'CRUO' || config.role == 'CRUA') {
      return true;
    } else if (config.role == 'CRUM') {
      return false;
    }
    return false;
  },
  /**
   * 初始化主持人
   * @param callback
   */
  initModerator: function (callback) {
    var self = this;
    try {
      if (typeof(config.getroomnode) == "function") {
        var roomNum = config.getroomnode();
      } else if (window.roomName != null) {
        var roomNum = window.roomName.split('@')[0];
      } else {
        var roomNum = null;
      }
      if (roomNum) {
        var _header = this.authoredHeader()
        $.ajax({
          url: window.serverPath + "/user/initModerator?j=" + connection.jid + "&r=" + roomNum,
          // dataType: 'jsonp',
          dataType: 'json',
          processData: false,
          headers: _header,
          type: 'get',
          async: false,
          success: function (rs) {
            if (rs.code == 200) {
              config.role = rs.obj[0];
              var isModerator = self.isAdmin();
              connection.emuc.addConfigRoleToPresence(config.role);
              connection.emuc.addIsModeratorToPresence(isModerator);
              connection.emuc.sendPresence();
              // update myself 'is_moderator' status
              for(var i = 0 ; i < window.participants.length; i++){
                var pt = window.participants[i];
                if(pt.jid == connection.jid){
                  pt.is_moderator = isModerator;
                  window.participants[i] = pt;
                }
              }
              callback({
                code: ManisResponse.code_200,
                msg: ManisResponse.init_moderator_complete,
                response: isModerator,
                master: Strophe.getResourceFromJid(rs.obj[1]),
                errorCode: ErrorCode.no_error
              });
            }
          },
          error: function (XMLHttpRequest, textStatus, errorThrown) {
            callback({
              code: ManisResponse.code_500,
              msg: ManisResponse.init_moderator_failed,
              errorCode: ErrorCode.init_moderator_right_failed
            });
          }
        });
      }
    } catch (e) {
      callback({
        code: ManisResponse.code_500,
        msg: ManisResponse.init_moderator_failed,
        errorCode: ErrorCode.api_fetch_failed
      });
    }

  },

