  /**
   *
   * @param jid
   * @returns {*}
   */
  getUserInfoByJid: function (jid) {
    if (window.participants.length) {
      for (var k = 0; k < window.participants.length; k++) {
        var p = window.participants[k];
        if (p.resource == Strophe.getResourceFromJid(jid)) {
          // hide jid
          return {
            nickname: p.nickname,
            resource: p.resource,
            jid: jid
          }
        }
      }
    }
    return {resource: Strophe.getResourceFromJid(jid), nickname: '', jid: jid};
  }
  ,

  /**
   * 通过resource获取用户
   * @param resource
   * @returns {*}
   */
  getUserByResource: function (resource) {
    if (window.participants.length) {
      for (var k = 0; k < window.participants.length; k++) {
        var p = window.participants[k];
        if (p.resource == resource) {
          // hide jid
          return p;
        }
      }
    }
    return null;
  }
  ,

