  sendChangeMainBlockSourceMsg: function (objectiveSource, objectSSRC, callback, isScreen) {
    var isScreenStream = isScreen ? true : false;
    var msg = $msg({to: connection.emuc.roomjid, type: 'groupchat'});//grant
    var participant = this.getUserByResource(objectiveSource);
    if (participant && participant.jid) {
      var _jid = participant.jid;
    } else {
      var _jid = connection.emuc.roomJid + '/' + objectiveSource;
    }
    msg.c('body', {
      xmlns: 'http://igniterealtime.org/protocol/stage',
      jid: _jid,
      screen: isScreenStream
    }).t(objectiveSource).up();
    msg.c('resource', {xmlns: 'http://igniterealtime.org/protocol/endpoint'}).t(objectiveSource).up();
    if (objectSSRC) {
      msg.c('ssrc', {xmlns: 'http://igniterealtime.org/protocol/streamssrc'}).t(objectSSRC).up();
    }
    // console.log('send change display mode msg : ', msg.tree());
    window.connection.send(msg);
    callback({
      code: ManisResponse.code_200,
      msg: ManisResponse.lock_main_display_complete,
      response: objectiveSource,
      errorCode: ErrorCode.no_error
    });
    $(document).trigger('manis.main.display.changed',[objectiveSource, objectSSRC]);
    var _this = this;
    if(_this.currentMainResource != objectiveSource || _this.currentMainSSRC != objectSSRC){
      try {
        var _header = _this.authoredHeader();
        $.ajax({
          url: window.serverPath + "/user/setStage?j=" + _jid + "&r=" + window.roomName.split('@')[0],
          // dataType: 'jsonp',
          dataType: 'json',
          processData: false,
          headers: _header,
          type: 'get',
          async: false,
          success: function (rs) {
            if (rs.code == 200) {
              console.log('set stage success! ');
              _this.currentMainResource = objectiveSource;
              _this.currentMainSSRC = objectSSRC;
            }
          },
          error: function (XMLHttpRequest, textStatus, errorThrown) {
            console.error('set stage failure!', XMLHttpRequest, textStatus, errorThrown);
          }
        });
      } catch (e) {
      }
    }
  },

