  /**
   * 设置带宽,兼容协商与不重新协商
   * @param bandWidth
   * @param callback
   */
  setVideoBandwidth: function (bandWidth, callback) {
    var _this = this;
    config.videoBandwidth = bandWidth;
    var conferenceHandler = window.activecall || null;
    if(!conferenceHandler){
      if(callback && typeof(callback) == 'function'){
        callback();
      }
      return;
    }
    var peerconnection = conferenceHandler.peerconnection.peerconnection;
    if (RTC.browser === 'chrome' &&
      'RTCRtpSender' in window &&
      'setParameters' in window.RTCRtpSender.prototype) {
      console.log('----------oooooooooooooooooooooooooooo-------------', 'change video band width to ', bandWidth, ' over fly')
      var videoStream = connection.jingle.localVideo;
      var videoTrack = null
      if (videoStream) {
          videoTrack = videoStream.getVideoTracks()[0];
      }
      var sender = _this.getSender(peerconnection.getSenders(), 'video', videoTrack ? videoTrack.id : null) ;
      if (!sender) {
          console.warn('--oooooooooooooooooooooooooooo-- cant find sender by track with: ', videoTrack, ' in senders ', peerconnection.getSenders());
          return false;
      }
      var parameters = sender.getParameters();
      parameters.encodings[0].maxBitrate = bandWidth * 1000;
      sender.setParameters(parameters).then(function () {
          if(callback && typeof(callback) == 'function'){
            console.log('set video bandwidth without negotiate: ', bandWidth);
            callback();
          }
        })
        .catch(function(e){
          console.error(e);
        });
      return;

    }else {
      console.log('----------oooooooooooooooooooooooooooo-------------', 'start to peer connection negotiate')
      conferenceHandler.peerconnectionNegotiate(function () {
        if(callback && typeof(callback) == 'function'){
          callback();
        }
        return;
      });
    }
  },


  /**
   *  获取对应的sender
   * @param senders
   * @param type
   * @param trackId
   * @returns {*}
   */
  getSender: function (senders, type, trackId) {
    var returnSender = null;
    if(!senders || !senders.length){
      return returnSender;
    }
    for (var i = 0; i < senders.length; i++) {
      var sender = senders[i];
      if(sender && sender.track.kind == type && sender.track.id == trackId){
          console.log('-------------oooooooooooooooooooooo--get the right sender------------', sender)
          returnSender = sender;
      }
    }
    return returnSender;
  },


