/**
 * Starts the {@link StatsCollector} if the feature is enabled in config.js.
 */
function startRtpStatsCollector() {
  stopRTPStatsCollector();
  if (config.enableRtpStats) {
    statsCollector = new StatsCollector(
      getConferenceHandler().peerconnection, 200, audioLevelUpdated, 2000,
        ConnectionQuality.updateLocalStats);
    statsCollector.start();
  }
}


function stopRTPStatsCollector() {
  if (statsCollector) {
    statsCollector.stop();
    statsCollector = null;
    ConnectionQuality.stopSendingStats();
  }
}

function getConferenceHandler() {
  return activecall;
}

function audioLevelUpdated(jid, audioLevel) {
  var resourceJid;
  if (jid === LocalStatsCollector.LOCAL_JID) {
    resourceJid = AudioLevels.LOCAL_LEVEL;
    if (isAudioMuted()) {
      audioLevel = 0;
    }
  }
  else {
    resourceJid = Strophe.getResourceFromJid(jid);
  }

  AudioLevels.updateAudioLevel(resourceJid, audioLevel);
}

/**
 * Checks whether the audio is muted or not.
 * @returns {boolean} true if audio is muted and false if not.
 */

function isAudioMuted() {
  var localAudio = connection.jingle.localAudio;
  for (var idx = 0; idx < localAudio.getAudioTracks().length; idx++) {
    if (localAudio.getAudioTracks()[idx].enabled === true)
      return false;
  }
  return true;
};


function disposeConference(onUnload) {
  var handler = getConferenceHandler();
  if (handler && handler.peerconnection) {
    // FIXME: probably removing streams is not required and close() should
    // be enough
    try {
        if (connection.jingle.localAudio) {
            handler.peerconnection.removeStream(connection.jingle.localAudio, onUnload);
        }
        if (connection.jingle.localVideo) {
            handler.peerconnection.removeStream(connection.jingle.localVideo, onUnload);
        }
        if (connection.jingle.localScreen) {
            handler.peerconnection.removeStream(connection.jingle.localScreen, onUnload);
            connection.jingle.localScreen = null;
            window.localSSRCS = [];
        }
    } catch (e) {
        console.error(e)
    }
    handler.peerconnection.close();
  }
  stopRTPStatsCollector();
  if (onUnload) {
    stopLocalRtpStatsCollector();
  }
  connection.ofmuc.appDestory();
  connection.ofmuc.appStop();
  activecall = null;
}

/**
 * Stops the {@link LocalStatsCollector}.
 */
function stopLocalRtpStatsCollector() {
  if (localStatsCollector) {
    localStatsCollector.stop();
    localStatsCollector = null;
  }
}

function modifyScreenStream(stream, successCallback, errorCallback, isRemove) {
  var conferenceHandler = getConferenceHandler();
  if (!conferenceHandler) {
    errorCallback({
      code: ManisResponse.code_404,
      msg: ManisResponse.room_unopend,
    });
    return;
  }
  var oldStream = connection.jingle.localVideo;
  conferenceHandler.switchStreams(stream, oldStream, successCallback);
};

function addScreenSourceToConnection(stream, callback) {
  var conferenceHandler = getConferenceHandler();
  if (!conferenceHandler) {
    callback({
      code: ManisResponse.code_404,
      msg: ManisResponse.room_unopend,
      errorCode: ErrorCode.conference_room_un_open
    });
    return;
  }
  //@TODO add share screen in new functions
  conferenceHandler.sourceOperation(stream, callback, "add");
  // conferenceHandler.addScreen(stream, callback);
};

function removeScreenSourceFromConnection(callback) {
  var conferenceHandler = getConferenceHandler();
  if (!conferenceHandler) {
    callback({
      code: ManisResponse.code_404,
      msg: ManisResponse.room_unopend,
      errorCode: ErrorCode.conference_room_un_open
    });
    return;
  }
  var screenStream = connection.jingle.localScreen;
    conferenceHandler.sourceOperation(screenStream, function (res) {
        if (screenStream.getVideoTracks().length) {
            screenStream.getVideoTracks().forEach(function (track) {
                track.stop();
            });
        }
        callback(res)
    }, "remove");
  // conferenceHandler.removeScreen(screenStream, callback);
  //   conferenceHandler.sourceOperation(screenStream, callback, "remove");
};


function addLocalStreamToConnection(mediaStream, callback) {
  var conferenceHandler = getConferenceHandler();
  if(mediaStream.getVideoTracks().length){
    connection.emuc.addVideoInfoToPresence(false);
  }
  if(mediaStream.getAudioTracks().length){
    connection.emuc.addAudioInfoToPresence(false);
  }
  if (!conferenceHandler) {
    if (window.webkitMediaStream) {
      var audioStream = new webkitMediaStream();
      var videoStream = new webkitMediaStream();
      var audioTracks = mediaStream.getAudioTracks();
      var videoTracks = mediaStream.getVideoTracks();
      for (var i = 0; i < audioTracks.length; i++) {
        audioStream.addTrack(audioTracks[i]);
      }
      for (i = 0; i < videoTracks.length; i++) {
        videoStream.addTrack(videoTracks[i]);
      }
      connection.jingle.localAudio = audioStream;
      connection.jingle.localVideo = videoStream;
    } else {
      connection.jingle.localAudio = mediaStream;
      connection.jingle.localVideo = mediaStream;
    }
    // 发送IQ消息到xmpp进入会议室.
    // mediaStream.src = URL.createObjectURL(mediaStream);
    mediaStream.ssrc = window.localStreamSSRC;
    callback({
      code: ManisResponse.code_200,
      msg: ManisResponse.add_local_stream_without_negiration_success,
      errorCode: ErrorCode.no_error,
      response: mediaStream,
    });
    return;
  }
  conferenceHandler.addLocalStream(mediaStream, callback);
};



function removeLocalStreamFromConnection(callback) {
  var conferenceHandler = getConferenceHandler();
  if (!conferenceHandler) {
    callback({
      code: ManisResponse.code_404,
      msg: ManisResponse.room_unopend,
      errorCode: ErrorCode.conference_room_un_open
    });
    return;
  }
  var localStreams = [];
  if(connection.jingle.localAudio){
    localStreams.push(connection.jingle.localAudio);
  }
  if(connection.jingle.localVideo){
    localStreams.push(connection.jingle.localVideo);
  }
  conferenceHandler.removeLocalStream(localStreams, callback);
};


function changeResolutionByApplyConstraints(resolution, videoInput, callback) {
  var _this = this;
  console.log('try to apply resolution: ', resolution, ' current resolution: ', _this.currentTargetResolution, ' current input: ', _this.currentTargetVideoInput);
  var mediaStream = connection.jingle.localVideo;
  if(this.currentTargetResolution == resolution && this.currentTargetVideoInput == videoInput && mediaStream.getVideoTracks().length){
    // not changed, just return;
    if(config.debug == true){
      console.log('resolution and video input device is not changed, maybe video stop, reset again');
    }
    return false;
  }
  var newResolutionConstraints = ManisFunctions.getConstraintsByResolution(false, true, null, videoInput, resolution);
  console.log('new resolution constraints : ', newResolutionConstraints,mediaStream);
  const constraints = {
    width: newResolutionConstraints.video.width,
    height: newResolutionConstraints.video.height,
  };
  if(mediaStream && constraints){
    const track = mediaStream.getVideoTracks()[0];
    if(track.applyConstraints != undefined){
      track.applyConstraints(constraints)
        .then(function (result) {
          var options = {
            videoInput: videoInput,
            resolution: resolution,
            replace: false,
          };
          options.stream = mediaStream;
          config.currentResolution = resolution;
          _this.currentTargetResolution = resolution;
          _this.currentTargetVideoInput = videoInput;
          ManisFunctions.setVideoBandwidth(config.videoBandwidth, function () {
            console.log('Change bandwidth on the fly complete 2');
          });
          callback({
            code: ManisResponse.code_200,
            msg: ManisResponse.change_video_resolution_complete,
            response: options,
            errorCode: ErrorCode.no_error
          });
          // Do something with the track such as using the Image Capture API.
          console.log('mediaStream apply contraints complete', newResolutionConstraints)
        }).catch(function(e){
        // The constraints could not be satisfied by the available devices.
        console.info('mediaStream apply contraints failed : ', e);
        if(e.name.match(/OverconstrainedError/) || e.constructor.name.match(/OverconstrainedError/)){
          // if height > 180 or width > 320 , try get lower resolution; else trow error
          if(parseInt(resolution) > 180){
            if (newResolutionConstraints.video.height.exact == 1080) {
              _this.changeResolutionByApplyConstraints('720',   videoInput, callback);
              return;
            } else if (newResolutionConstraints.video.height.exact == 720) {
              _this.changeResolutionByApplyConstraints('768',  videoInput, callback);
              return;
            }else if (newResolutionConstraints.video.height.exact == 768) {
              _this.changeResolutionByApplyConstraints('540',  videoInput, callback);
              return;
            } else if (newResolutionConstraints.video.height.exact == 540) {
              _this.changeResolutionByApplyConstraints('480',  videoInput, callback);
              return;
            }else if (newResolutionConstraints.video.height.exact == 480) {
              _this.changeResolutionByApplyConstraints('240',  videoInput, callback);
              return;
            }else if (newResolutionConstraints.video.height.exact == 240) {
              _this.changeResolutionByApplyConstraints('180',  videoInput, callback);
              return;
            }
          }else {
            console.error('Over lest constrained error', e);
            throw Error('Over lest constrained error');
          }
        }else {
          console.error('Other error : ', e);
          throw Error('Other error ');
        }
      })
    }else {
      console.error('ApplyConstraints undefined error ');
      throw Error('ApplyConstraints undefined error ');
    }
  }

}

function modifyLocalVideoStream(resolution, _videoInput, callback, loop) {

  var oldStream = connection.jingle.localVideo;

  if(this.currentTargetResolution == resolution && this.currentTargetVideoInput == _videoInput && oldStream.getVideoTracks().length){
    // not changed, just return;
    if(config.debug == true){
      console.log('resolution and video input device is not changed, maybe video stop, reset again');
    }
    return false;
  }

  if(loop !== true){
    this.currentTargetResolution = resolution;
    this.currentTargetVideoInput = _videoInput;
  }

  var conferenceHandler = getConferenceHandler();

  if (oldStream.getVideoTracks().length) {
    oldStream.getVideoTracks().forEach(function (track) {
      if(config.debug == true){
        console.log('remove video track : ', track);
      }
      track.stop();
    });
  }

  var newResolutionConstraints = ManisFunctions.getConstraintsByResolution(false, true, null, _videoInput, resolution);
  if(config.debug == true){
    console.log('will get new stream by new resolution : ', newResolutionConstraints);
  }

  ManisStream.getMedia(newResolutionConstraints, function (stream) {
    config.currentResolution = resolution;
    var options = {
      videoInput: _videoInput,
      resolution: resolution,
      replace: true,
    };
    // stream.src = URL.createObjectURL(stream);
    stream.ssrc = window.localStreamSSRC;
    if (window.connectType == 'p2p') {
      P2P.switchStream(oldStream, stream, callback, options);
    } else {
      connection.jingle.localVideo = stream;
      options.resource = Strophe.getResourceFromJid(connection.jid);
      options.stream = stream;
      callback({
        code: ManisResponse.code_200,
        msg: ManisResponse.change_video_resolution_complete,
        response: options,
        errorCode: ErrorCode.no_error
      });
      if(conferenceHandler && conferenceHandler.peerconnection.negotiationInprogress == false){
        conferenceHandler.replaceMediaTrack(stream, oldStream, function () {
          if (options.stream.ssrc != window.localStreamSSRC) {
            options.stream.ssrc = window.localStreamSSRC;
          }
          callback({
            code: ManisResponse.code_200,
            msg: ManisResponse.change_video_resolution_complete,
            response: options,
            errorCode: ErrorCode.no_error
          })
        })
      }else {
        setTimeout(function () {
          modifyLocalVideoStream(resolution, _videoInput, callback);
          return;
        }, 1000);
      }

    }

  }, function (error) {
    if (error.name == 'DevicesNotFoundError' || error.constructor.name == 'DevicesNotFoundError'
        ||error.name == 'NotFoundError' || error.constructor.name == 'NotFoundError') {
      callback({
        code: ManisResponse.code_404,
        msg: ManisResponse.without_usefull_video_device,
        errorCode: ErrorCode.media_device_except
      });
      return;
    }
    if (error.name == 'NotReadableError') {
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.get_user_media_failed,
        error: error,
        errorCode: ErrorCode.media_device_get_local_media_failed
      });
      return;
    }
    if (error.name == 'OverconstrainedError' || error.constructor.name == 'OverconstrainedError') {
      if (newResolutionConstraints.video.width.exact > 320 || newResolutionConstraints.video.height.exact > 180) {
        // turn down video constraint
        if (newResolutionConstraints.video.height.exact == 1080) {
          modifyLocalVideoStream('720',   _videoInput, callback);
          return;
        } else if (newResolutionConstraints.video.height.exact == 720) {
          modifyLocalVideoStream('768',  _videoInput, callback);
          return;
        }else if (newResolutionConstraints.video.height.exact == 768) {
          modifyLocalVideoStream('540',  _videoInput, callback);
          return;
        } else if (newResolutionConstraints.video.height.exact == 540) {
          modifyLocalVideoStream('480',  _videoInput, callback);
          return;
        }else if (newResolutionConstraints.video.height.exact == 480) {
          modifyLocalVideoStream('240',  _videoInput, callback);
          return;
        }else if (newResolutionConstraints.video.height.exact == 240) {
          modifyLocalVideoStream('180',  _videoInput, callback);
          return;
        }
      }
    }
    console.warn(error)
    callback({
      code: ManisResponse.code_402,
      msg: ManisResponse.unsupport_resolution,
      errorCode: ErrorCode.media_device_resolution_nonsupport
    });
  })

};

function modifyLocalAudioStream(_audioInput, callback) {
  var conferenceHandler = getConferenceHandler();
  var oldStream = connection.jingle.localAudio;
  if (oldStream.getAudioTracks().length) {
    oldStream.getAudioTracks().forEach(function (track) {
      if(config.debug == true){
        console.log(' remove audio track : ', track);
      }
      track.stop();
    });
  }

  var newAudioInConstraints = ManisFunctions.getConstraintsByResolution(true, false, _audioInput, null, null);
  ManisStream.getMedia(newAudioInConstraints, function (stream) {
    // before conference start
    if(!conferenceHandler){
      connection.jingle.localAudio = stream;
      callback({
        code: ManisResponse.code_200,
        msg: ManisResponse.audio_input_change_completed,
        errorCode: ErrorCode.no_error
      });
      return;
    }
    var options = {
      activeAudioInput: _audioInput,
      replace: true,
    };
    options.stream = stream;
    if(conferenceHandler.peerconnection.negotiationInprogress == false) {
      conferenceHandler.replaceMediaTrack(stream, oldStream, function () {
        callback({
          code: ManisResponse.code_200,
          msg: ManisResponse.audio_input_change_completed,
          response: options,
          errorCode: ErrorCode.no_error
        });
      }, 'audio');
    }else {
      setTimeout(function () {
        modifyLocalAudioStream(resolution, _videoInput, callback);
        return;
      }, 1000);
    }
  }, function (error) {
    callback({
      code: ManisResponse.code_400,
      msg: ManisResponse.audio_input_change_failed,
      error: error,
      errorCode: ErrorCode.change_audio_input_device_failed
    });
  })
}

function generateConstraints(withAudio, withVideo, audioInput, videoInput, videoResolution){
  var constraints = {
    audio: withAudio ? {} : false,
    video: withVideo ? {} : false
  };
  if(withAudio === true){
    constraints.audio.deviceId =  audioInput ? {exact:  audioInput} : undefined;
  }
  if(withVideo === true){
    constraints.video.deviceId =  videoInput ? {exact: videoInput} : undefined;
  }
  if(withVideo === true && videoResolution){
    switch (videoResolution.toString()){
      case '1080':
        constraints.video.width = {exact: 1920};
        constraints.video.height = {exact: 1080};
        config.videoBandwidth = (config.videoBandwidth == 1024 * 2 * 2) ? config.videoBandwidth : 1024 * 2;
        break;
      case '720':
        constraints.video.width = {exact: 1280};
        constraints.video.height = {exact: 720};
        config.videoBandwidth = (config.videoBandwidth == 512 * 2 * 2) ? config.videoBandwidth : 512 * 2;
        break;
      case '768':
        constraints.video.width = {exact: 1024};
        constraints.video.height = {exact: 768};
        config.videoBandwidth = (config.videoBandwidth == 512 * 2 * 2) ? config.videoBandwidth : 512 * 2;
        break;
      case '540':
        constraints.video.width = {exact: 960};
        constraints.video.height = {exact: 540};
        config.videoBandwidth = (config.videoBandwidth == 256 * 2 * 2) ? config.videoBandwidth : 256 * 2;
        break;
      case '480':
        constraints.video.width = {exact: 640};
        constraints.video.height = {exact: 480};
        config.videoBandwidth = (config.videoBandwidth == 256 * 2 * 2) ? config.videoBandwidth : 256 * 2;
        break;
      case '240':
        constraints.video.width = {exact: 320};
        constraints.video.height = {exact: 240};
        config.videoBandwidth = (config.videoBandwidth == 128 * 2 * 2) ? config.videoBandwidth : 128 * 2;
        break;
      case '180':
        constraints.video.width = {exact: 320};
        constraints.video.height = {exact: 180};
        config.videoBandwidth = (config.videoBandwidth == 128 * 2 * 2) ? config.videoBandwidth : 128 * 2;
        break;
      default:
        constraints.video.width = {exact: 320};
        constraints.video.height = {exact: 180};
        config.videoBandwidth = (config.videoBandwidth == 128 * 2 * 2) ? config.videoBandwidth : 128 * 2;
        break;
    }
  }
  return constraints;
}

function getDevices(type, callback) {
  var audioInputDevices = [];
  var audioOutputDevices = [];
  var videoInputDevices = [];
  var otherDevices = [];
  var allDevices = [];

  navigator.mediaDevices.enumerateDevices().then(function (deviceInfos) {
    for (var i = 0; i !== deviceInfos.length; ++i) {
      var deviceInfo = deviceInfos[i];
      //console.log("-----deviceInfo--------", deviceInfo);
      var device = {value:deviceInfo.deviceId};
      if (deviceInfo.kind === 'audioinput') {
          //由于safari  deviceInfo 无法获取output 这里做一下处理
          if(RTC.isSafari() || RTC.isFirefox()) {
              device.label = deviceInfo.label || 'speaker ' + (audioOutputDevices.length + 1);
              audioOutputDevices.push(device);
          }
          device.label = deviceInfo.label || 'microphone ' + (audioInputDevices.length + 1);
         audioInputDevices.push(device);
      } else if (deviceInfo.kind === 'audiooutput') {
        device.label = deviceInfo.label || 'speaker ' + (audioOutputDevices.length + 1);
        audioOutputDevices.push(device);
      } else if (deviceInfo.kind === 'videoinput') {
        device.label = deviceInfo.label || 'camera ' + (videoInputDevices.length + 1);
        videoInputDevices.push(device);
      } else {
        device.label = deviceInfo.label || 'Some other kind of source/device: ';
        otherDevices.push(device);
      }
    }
    allDevices = {
      audioInputDevices: audioOutputDevices,
      audioOutputDevices: audioOutputDevices,
      videoInputDevices: videoInputDevices,
      otherDevices: otherDevices
    };
    if (type == 'audioinput') {
      var returnData = audioInputDevices;
    } else if (type == 'audiooutput') {
      var returnData = audioOutputDevices;
    } else if (type == 'videoinput') {
      var returnData = videoInputDevices;
    } else {
      var returnData = allDevices;
    }
    callback({
      code: ManisResponse.code_200,
      msg: ManisResponse.find_devices,
      response: returnData,
      errorCode: ErrorCode.no_error
    })
  }).catch(
    function () {
      callback({
        code: ManisResponse.code_404,
        msg: ManisResponse.unfind_devices,
        errorCode: ErrorCode.media_device_except
      })
    });
};


function sendOnlyChannelMsg(callback) {
  if(activecall && activecall.peerconnection){
    var channel = activecall.peerconnection.channel;
    if(channel){
      var videoSendOnly = {colibriClass: "AttendMessageEvent",type: "sendonly"};
      var msg = JSON.stringify(videoSendOnly);
      try{
        console.log('will send sendonly channel msg: ', msg);
        channel.send(msg);
        callback({
          code: ManisResponse.code_200,
          msg: ManisResponse.message_send_complete
        })
      }catch (e){
        console.error('send channel msg failed: ', e);
        callback({
          code: ManisResponse.code_400,
          msg: ManisResponse.send_inviste_failed
        })
      }
    }else {
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.send_inviste_failed
      })
    }
  }
}


