var strophe = require('strophe');
var muc = require('./strophe/plugins/strophe.emuc');

var Openfire = require('./libs/strophe.openfire.js');
var ManisStream = require('./manis.stream');
var ManisResponse = require('../modules/manis.response');
var ManisFunctions = require('../modules/manis.functions');

var peers = {}, myjid;


function connectXMPP(username, password, nickname, successCallback, errorCallback) {
  var openfireHost = window.serverPath + '/http-bind/';
  connection = new Openfire.Connection(openfireHost);
  try {
    connection.connect(username, password, onConnect);
    if (nickname) {
      config.nickname = nickname;
      connection.emuc.addDisplayNameToPresence(nickname);
    }
    connection.addHandler(onMessage, null, 'message');
    connection.addHandler(onJingle, 'urn:xmpp:jingle:1', 'iq', 'set', null, null);
    successCallback({
      code: ManisResponse.code_200,
      msg: ManisResponse.sign_success
    });
    console.info('userId : ', username.split('@')[0]);
  } catch (e) {
    errorCallback({
      code: ManisResponse.code_400,
      msg: ManisResponse.sign_failed,
      error: e
    })
  }

}


function onConnect(status) {
  if (status == Strophe.Status.CONNECTED) {
    window.connection = connection;
    console.log("xmpp connected");
    connection.sendIQ($pres().tree());
  }
}

function onMessage(msg) {
  if (window.connectType != 'p2p') {
    return;
  }
  console.log(msg);
  roomId = msg.getAttribute('from');
  myjid = msg.getAttribute("to");
  var join = $(msg).find('>join');
  if (join.length) {
    var jids = join[0].childNodes;
    for (var i = 0; i < jids.length; i++) {
      var jid = jids[i].getAttribute("jid");
      if (jid == myjid) {
        continue;
      }
      if (jid == roomId) {
        continue;
      }
      var pc = getOrCreatePeerConnection(jid);
      createOffer(pc, jid);
    }
  }
  var left = $(msg).find('>left');
  if (left.length) {
    var sid = Strophe.getResourceFromJid(left[0].childNodes[0].textContent);
    // var remoteVideos = document.getElementById("remoteVideos");
    // remoteVideos.removeChild(document.getElementById(sid));
    if (window.participants.length > 0) {
      for (var k = 0; k <= window.participants.length; k++) {
        var p = window.participants[k];
        if (p && p.resource === Strophe.getResourceFromJid(sid)) {
          window.participants.splice(k, 1);
        }
      }
    }
    $(document).trigger('manis.room.someone.left', {resource: Strophe.getResourceFromJid(sid)});
    peers[sid].close();
    delete peers[sid];
    console.log("sid>>>>", sid);
  }
  console.log("peers>>>>>>>>", peers);
  return true;
}

function onJingle(iq) {
  if (window.connectType != 'p2p') {
    return;
  }
  var rid = $(iq).find('jingle').attr('rid');
  var sid = $(iq).find('jingle').attr('sid');
  var pc = getOrCreatePeerConnection(sid);
  var action = $(iq).find('jingle').attr('action');
  var sdp = $(iq).find('jingle').attr('sdp').split("||").join("\r\n");
  roomId = iq.getAttribute('from');
  console.log("action>>>>>>>>>>>>>>>>:", action, "   rid:", rid);
  if ('session-initiate' == action) {
    myjid = rid;

    pc.setRemoteDescription(new RTCSessionDescription({
      type: 'offer',
      sdp: sdp
    }));
    pc.createAnswer(function (sessionDescription) {
      //console.log("--------------------------发送本地消息-----------------------------", sessionDescription);
      pc.setLocalDescription(sessionDescription);
      var init = $iq({
        to: roomId,
        type: 'set'
      }).c('jingle', {
        xmlns: 'urn:xmpp:jingle:1',
        action: 'session-accept',
        sid: sid,
        rid: rid,
        sdp: sessionDescription.sdp.split("\r\n").join("||")
      });
      console.log("发送 createAnswer", init.tree());
      connection.sendIQ(init);
    }, function (error) {
      console.log('Failure callback: ' + error);
    });
  }
  if ('session-accept' == action) {
    pc.setRemoteDescription(new RTCSessionDescription({
      type: 'answer',
      sdp: sdp
    }));
  }
  if ('transport-info' == action) {
    var cand = $(iq).find('jingle').attr('sdp').split("||");
    var candidate = new RTCIceCandidate({
      candidate: cand[0],
      sdpMLineIndex: cand[1],
      sdpMid: cand[2]
    });
    pc.addIceCandidate(candidate);
  }
  return true;
}

function getOrCreatePeerConnection(sid) {
  var pc = peers[Strophe.getResourceFromJid(sid)];
  if (!pc) {
    var server = {"iceServers": [{"url": config.nat}]};
    pc = new RTCPeerConnection(server);
    pc.onicecandidate = function (event) {
      if (event.candidate) {
        var init = $iq({
          to: roomId,
          type: 'set'
        }).c('jingle', {
          xmlns: 'urn:xmpp:jingle:1',
          action: 'transport-info',
          sid: sid,
          rid: myjid,
          sdp: event.candidate.candidate + "||" + event.candidate.sdpMLineIndex + "||" + event.candidate.sdpMid
        });
        console.log("发送 event.candidate", sid, " myjid:", myjid);
        connection.sendIQ(init);
      } else {
        console.log("End of candidates.");
      }
    };
    pc.onaddstream = function (event) {
      /*console.log("remote:" + event.stream.id);
       var remoteVideos = document.getElementById("remoteVideos");
       var newVideo = document.createElement("video");
       newVideo.setAttribute("id", Strophe.getResourceFromJid(sid));
       attachMediaStream(newVideo, event.stream);
       remoteVideos.appendChild(newVideo);*/
      // event.stream.src = URL.createObjectURL(event.stream);
      var participant = {
        info: ManisFunctions.getUserInfoByJid(sid),
        stream: event.stream,
        status: ManisFunctions.generateStatusNode(Strophe.getResourceFromJid(sid))
      };
      $(document).trigger('manis.room.someone.joined', [participant]);
    };
    pc.onremovestream = function (event) { // 远程视频移除
      console.log("远程视频移除", event);
    };
    pc.addStream(localStream);
    peers[Strophe.getResourceFromJid(sid)] = pc;
  }
  // console.log(peers);
  return pc;
}


function createOffer(pc, sid) {
  pc.createOffer(function (sessionDescription) {
    //console.log("--------------------------发送本地消息-----------------------------", sessionDescription);
    pc.setLocalDescription(sessionDescription);
    var init = $iq({
      to: roomId,
      type: 'set'
    }).c('jingle', {
      xmlns: 'urn:xmpp:jingle:1',
      action: 'session-initiate',
      sid: sid,
      rid: myjid,
      sdp: sessionDescription.sdp.split("\r\n").join("||")
    });
    console.log("event.createOffer", init.tree());
    connection.sendIQ(init);
  }, function (error) {
    console.log('Failure callback: ' + error);
  });
}


function joinP2PRoom(roomName, successCallback, errorCallback) {

  ManisStream.getMedia(constraints, function (stream) {
    var mediaStream = localStream = stream;
    /* console.log("local stream id:", stream.id);
     var container = document.getElementById("localVideos");
     var videoElement = document.createElement('video');
     videoElement.id = stream.id;
     container.appendChild(videoElement);
     attachMediaStream(videoElement, stream);*/
    if (window.webkitMediaStream) {
      var audioStream = new webkitMediaStream();
      var videoStream = new webkitMediaStream();
      var audioTracks = mediaStream.getAudioTracks();
      var videoTracks = mediaStream.getVideoTracks();
      for (var i = 0; i < audioTracks.length; i++) {
        audioStream.addTrack(audioTracks[i]);
      }
      for (i = 0; i < videoTracks.length; i++) {
        videoStream.addTrack(videoTracks[i]);
      }
      connection.jingle.localAudio = audioStream;
      connection.jingle.localVideo = videoStream;
    } else {
      connection.jingle.localAudio = mediaStream;
      connection.jingle.localVideo = mediaStream;
    }


    var _to = roomName + '@' + 'conference.' + manisConfg.messageServer;
    var iq = createConferenceIq(_to);
    connection.sendIQ(iq, function (result) {
      connection.emuc.doJoin(_to + "/" + Strophe.getResourceFromJid(connection.jid));
      var _resource = Strophe.getResourceFromJid(connection.jid);
      // localStream.src = URL.createObjectURL(localStream);
      var myself = {
        info: {
          nickname: config.nickname ? config.nickname : manisConfg.mUsername,
          resource: _resource
        },
        stream: localStream,
        status: ManisFunctions.generateStatusNode(_resource),
        constraints: constraints
      };
      successCallback({
        code: ManisResponse.code_200,
        msg: ManisResponse.join_room_success,
        response: myself
      });
    }, function (error) {
      errorCallback({
        code: ManisResponse.code_400,
        msg: ManisResponse.join_room_failed,
        error: error
      })
    });
  }, function (error) {
    if (error.name == 'DevicesNotFoundError' || error.constructor.name == 'DevicesNotFoundError'
        || error.name == 'NotFoundError' || error.constructor.name == 'NotFoundError') {
      errorCallback({
        code: ManisResponse.code_404,
        msg: ManisResponse.without_usefull_video_device
      });
      return;
    }
    if (error.name == 'OverconstrainedError' || error.constructor.name == 'OverconstrainedError') {
      if ((error.constraint == 'width' && constraints.video.width.exact > 320) || (error.constraint == 'height' && constraints.video.height.exact > 240)) {
        // turn down video constraint
        if (constraints.video.height.exact == 1080) {
          constraints.video.width.exact = 1280;
          constraints.video.height.exact = 720;
          joinP2PRoom(roomName, successCallback, errorCallback);
        } else if (constraints.video.height.exact == 720) {
          constraints.video.width.exact = 640;
          constraints.video.height.exact = 480;
          joinP2PRoom(roomName, successCallback, errorCallback);
        } else if (constraints.video.height.exact == 480) {
          constraints.video.width.exact = 320;
          constraints.video.height.exact = 240;
          joinP2PRoom(roomName, successCallback, errorCallback, constraints);
        }
      } else {
        errorCallback({
          code: ManisResponse.code_402,
          msg: ManisResponse.out_of_video_device_resolution
        });
        return;
      }
    }
  });
}

/**
 *
 * @param roomName
 * @returns {*}
 */
function createConferenceIq(roomName) {
  var elem = $iq({
    to: "p2p." + manisConfg.messageServer,
    type: 'set'
  });
  elem.c('conference', {
    xmlns: 'http://jitsi.org/protocol/focus',
    room: roomName
  });
  elem.up();
  return elem;
}

/**
 *
 * @param successCallback
 * @param errorCallback
 */
function p2pAudioToggleMute(successCallback, errorCallback) {

  var mute = !ManisFunctions.isAudioMuted();

  try {

    var localAudioTracks = connection.jingle.localAudio.getAudioTracks();
    if (localAudioTracks.length > 0) {
      for (var idx = 0; idx < localAudioTracks.length; idx++) {
        localAudioTracks[idx].enabled = !mute;
      }
    }
    connection.emuc.addAudioInfoToPresence(mute);
    connection.emuc.sendPresence();
    // @TODO 发送iq消息告诉会议室静音状态
    successCallback({
      code: ManisResponse.code_200,
      msg: ManisResponse.set_audio_toggle_complete,
      response: mute
    })

  } catch (e) {
    errorCallback({
      code: ManisResponse.code_400,
      msg: ManisResponse.set_audio_toggle_failed,
      response: !mute
    })
  }

}

/**
 *
 * @param successCallback
 * @param errorCallback
 */
function p2pVideoToggleMute(successCallback, errorCallback) {
  var mute = !ManisFunctions.isVideoMuted();
  try {
    var localVideoTracks = connection.jingle.localVideo.getVideoTracks();

    for (var i = 0; i < localVideoTracks.length; ++i) {
      localVideoTracks[i].enabled = !mute;
    }
    connection.emuc.addAudioInfoToPresence(mute);
    connection.emuc.sendPresence();
    // @TODO 发送iq消息告诉会议室开关视频状态
    successCallback({
      code: ManisResponse.code_200,
      msg: ManisResponse.set_video_toggle_complete,
      response: mute
    })
  } catch (e) {
    errorCallback({
      code: ManisResponse.code_400,
      msg: ManisResponse.set_video_toggle_failed,
      response: !mute
    })
  }
}

