$(document).bind('moderatored.muc', function (event, jid, isModerator) {
  var withModerator = false;
  for (var i = 0; i < window.participants.length; i++) {
    var participant = window.participants[i];
    if (participant.resource == Strophe.getResourceFromJid(jid)) {
      participant.is_moderator = isModerator;
    }
    if (participant.is_moderator) {
      withModerator = true;
    }
  }
  // console.log('XXXXXXXXXXXXXX with moderator status : ', withModerator, window.config.withModerator);
  // if (withModerator != window.config.withModerator) {
  //   $(document).trigger('manis.on.with.moderator.changed', [withModerator]);
  // }
});

$(document).bind('manis.been.stop.screen.share', function (event) {
  window.removeScreenInprogress = true;
  window.isUsingScreenStream = false;
  Status.removeScreenSourceFromConnection(function (result) {
    console.info('someone share screen replaced my share');
    $(document).trigger('mains.on.screen.share.end');
  })
});

$(document).bind('entered.muc', function (event, jid, info, pres) {
  console.log('entered', jid, info);

  // ignore recorder
  if(jid.indexOf('recorder') > -1){
    // console.log('recorder entered ');
    // $(document).trigger('manis.recorder.status.change', [true, jid]);
    return false;
  }

  // 判斷將新加入的用戶添加到用戶列表中，如果jid是focus，則添加自己進去，參會用戶participant對象為：
  // var participant = {
  //   nickname: 'nickname',
  //   jid: 'uniqueId@conference/resource',
  //   resource: 'resource',  截取jid後面的resource
  // }
  if (jid.indexOf('focus') === -1) {
    // remote participant
    var participant = {
      nickname: info.displayName,
      jid: info.jid,
      resource: Strophe.getResourceFromJid(info.jid),
      audio_muted: false,
      video_muted: false,
      is_moderator: (connection.jid == config.moderatorJid),
    }
  } else {
    // self
    var participant = {
      nickname: config.nickname ? config.nickname : manisConfg.mUserName,
      jid: connection.jid,
      resource: Strophe.getResourceFromJid(connection.jid),
      audio_muted: false,
      video_muted: false,
      is_moderator: (connection.jid == config.moderatorJid),
    }
  }
  // 去重
  if (window.participants.length > 0) {
    var participantExist = false;
    for (var k = 0; k < window.participants.length; k++) {
      var p = window.participants[k];
      if (p.resource === participant.resource) {
        participantExist = true;
      }
    }
    if (!participantExist) {
      window.participants.push(participant);
    }
  } else {
    // insert
    window.participants.push(participant);
  }
  if (info.isFocus) {
    window.focusJid = jid;
    console.info("Ignore focus: " + jid + ", real JID: " + info.jid);
    return;
  }
  $(document).trigger('manis.on.muc.change', ['entered', participant])
});

$(document).bind('left.muc', function (event, jid) {
  console.info("=========left==========:", event , " jid : ", jid);
  delete jid2Ssrc[jid];
  if (connection.jingle != undefined) {
    connection.jingle.terminateByJid(jid);
  }
  var leftFromRoom = false;
  // 從participants中刪除對應的participant對象
  if (window.participants.length > 0) {
    for (var k = 0; k <= window.participants.length; k++) {
      var p = window.participants[k];
      if (p && p.resource === Strophe.getResourceFromJid(jid)) {
        window.participants.splice(k, 1);
        leftFromRoom = true;
      }
    }
  }
  if(leftFromRoom){
    console.log("=======================left======================",jid);
    $(document).trigger('manis.room.someone.left', {resource: Strophe.getResourceFromJid(jid)});
    $(document).trigger('app.share.endpoint.left', [Strophe.getResourceFromJid(jid)]);
    $(document).trigger('manis.on.muc.change', ['left', jid])
  }else {
    console.log('some one sign out:', Strophe.getResourceFromJid(jid));
  }
  //
  if(jid.indexOf('recorder') > -1){
    $(document).trigger('manis.recorder.status.change', [false, jid]);
  }
});

$(document).bind('presence.status', function (event, pres) {
  try{
    var resources = {}, status = {};
    var presNode = $(pres);
    var from = presNode.attr('from');
    var configRole = presNode.find('configRole').text() || 'NONE';
    var nickName = presNode.find('nick').text() || '';
    var audioMuted = presNode.find('audiomuted').text() || false;
    var videoMuted = presNode.find('videomuted').text() || false;
    var statusNode = presNode.find('stats[xmlns="http://jitsi.org/jitmeet/stats"]');
    var mediaNode = presNode.find('media[xmlns="http://estos.de/ns/mjs"]');
    $.each(statusNode.find('stat'), function (index, statNode) {
      if($(statNode).attr('name') == 'bitrate_download'){
        var downloadBitrate = $(statNode).attr('value') || '0';
        status.downloadBitrate = downloadBitrate;
      }else if($(statNode).attr('name') == 'bitrate_upload'){
        var uploadBitrate = $(statNode).attr('value') || '0';
        status.uploadBitrate = uploadBitrate;
      }else if($(statNode).attr('name') == 'packetLoss_download'){
        var packageDownloadLost = $(statNode).attr('value') || '0';
        status.packageDownloadLost = packageDownloadLost;
      }else if($(statNode).attr('name') == 'packetLoss_upload'){
        var packageUploadLost = $(statNode).attr('value') || '0';
        status.packageUploadLost = packageUploadLost;
      }
    });
    $.each(mediaNode.find('source'), function (index, sourceNode) {
      var sourceSSRC = $(sourceNode).attr('ssrc');
      if($(sourceNode).attr('type') == 'audio'){
        resources.audio = {ssrc: sourceSSRC, resolution: (sourceSSRC ? ssrc2resolution[sourceSSRC]: 'NaN')};
      }else if($(sourceNode).attr('type') == 'video'){
        resources.video ={ssrc: sourceSSRC, resolution: (sourceSSRC ? ssrc2resolution[sourceSSRC]: 'NaN')};
      }else if($(sourceNode).attr('type') == 'screen'){
        resources.screen = {ssrc: sourceSSRC, resolution: (sourceSSRC ? ssrc2resolution[sourceSSRC]: 'NaN')};
      }
    });
    var participantPresence = {
      from: from,
      nickName: nickName,
      configRole: configRole,
      audioMuted: audioMuted,
      videoMuted: videoMuted,
      status: status,
      mediaSources: resources,
    };
    $(document).trigger('manis.participant.status.update', [participantPresence]);
    updateSSRC2Jid(pres, from);
  }catch (e){
    console.error('on presence.status error',e);
  }

});

function updateSSRC2Jid(pres, jid) {
  //  update
  $(pres).find('media[xmlns="http://estos.de/ns/mjs"]>source').each(function (idx, ssrc) {
    var ssrcV = ssrc.getAttribute('ssrc');
    ssrc2jid[ssrcV] = jid;
    var type = ssrc.getAttribute('type');
    ssrc2videoType[ssrcV] = type;
    // console.log('======= ssrcV :', ssrcV, ' ========= ssrc2jid : ', ssrc2jid, ' ==== type : ', type, ' ==== ssrc2videoType: ', ssrc2videoType);
  });
  updateConnectQuality(pres, jid)
}

function updateConnectQuality(pres, from) {

  var stats = $(pres).find('stats');
  if (stats.length) {
    var statsObj = {};
    Strophe.forEachChild(stats[0], "stat", function (el) {
      statsObj[el.getAttribute("name")] = el.getAttribute("value");
    });
    var videoSsrc = null;
    var media = $(pres).find('media');
    if (media.length) {
      Strophe.forEachChild(media[0], "source", function (sel) {
        var type = sel.getAttribute("type");
        if (type == 'video') {
          videoSsrc = sel.getAttribute("ssrc");
        }
      });
    }
    ConnectionQuality.updateRemoteStats(from, statsObj, videoSsrc);
  }
}

$(document).bind('presence.muc', function (event, jid, info, pres) {

  // Remove old ssrcs coming from the jid
  Object.keys(ssrc2jid).forEach(function (ssrc) {
    if (ssrc2jid[ssrc] == jid) {
      delete ssrc2jid[ssrc];
    }
  });
  updateSSRC2Jid(pres, jid);

  if (window.participants.length > 0) {
    for (var k = 0; k <= window.participants.length; k++) {
      var p = window.participants[k];
      if (p && p.resource === Strophe.getResourceFromJid(jid)) {
        window.participants[k].nickname = info.displayName;
        window.participants[k].jid = jid;
      }
    }
  }
});

