$(document).bind('setLocalDescription.jingle', function (event, sid) {
  // put our ssrcs into presence so other clients can identify our stream
  var sess = connection.jingle.sessions[sid];
  var newssrcs = [];
  var media = simulcast.parseMedia(sess.peerconnection.localDescription);
  media.forEach(function (media) {

    if (Object.keys(media.sources).length > 0) {
      // TODO(gp) maybe exclude FID streams?
      Object.keys(media.sources).forEach(function (ssrc) {
        newssrcs.push({
          'ssrc': ssrc,
          'type': media.type,
          'direction': media.direction
        });
      });
    }
    else if (sess.localStreamsSSRC && sess.localStreamsSSRC[media.type]) {
      newssrcs.push({
        'ssrc': sess.localStreamsSSRC[media.type],
        'type': media.type,
        'direction': media.direction
      });
    }

  });

  console.log('new ssrcs : ', newssrcs);

  // Have to clear presence map to get rid of removed streams
  connection.emuc.clearPresenceMedia();

  if (newssrcs.length > 0) {
    if(window.localSSRCS.length <= 0){
      window.localSSRCS = newssrcs;
    }
    for (var i = 1; i <= newssrcs.length; i++) {
      // Change video type to screen
      // if (newssrcs[i - 1].type === 'video') {
      //   window.localStreamSSRC = newssrcs[i - 1].ssrc;
      //   $(document).trigger('manis.local.stream.ssrc.update', [newssrcs[i - 1].ssrc, newssrcs[i - 1].type]);
      // }
      // if (newssrcs[i - 1].type === 'video' && isUsingScreenStream && i > 2) {
      //   newssrcs[i - 1].type = 'screen';
      // }

      var newSSRC = newssrcs[i-1];
      newSSRC.isOriginalExist = false;
      if(window.localSSRCS.length){
        for(var j = 0 ; j < window.localSSRCS.length; j++){
          var perSSRC = window.localSSRCS[j];
          if(perSSRC.ssrc == newSSRC.ssrc){
            newSSRC.isOriginalExist = true;
            newSSRC.type = perSSRC.type;
            if(newSSRC.type == 'video'){
              window.localStreamSSRC = newSSRC.ssrc;
              $(document).trigger('manis.local.stream.ssrc.update', [newSSRC.ssrc, newSSRC.type]);
            }
          }
        }
      }
      if(newSSRC.isOriginalExist == false && isUsingScreenStream){
        newSSRC.type = 'screen';
      }
      connection.emuc.addMediaToPresence(i,
        newSSRC.type, newSSRC.ssrc, newSSRC.direction);
    }

    connection.emuc.sendPresence();
  }
  state = false;
});

/**
 *
 * @param stream
 * @param jid
 */
function onRemoteVideoAdded(stream, jid) {
  var that = this;
  var type = checkStreamIsScreen(stream,stream.ssrc);
  stream.streamType = type;
  if(checkStreamTrackAlive(stream)){
    var participant = {
      info: ManisFunctions.getUserInfoByJid(jid),
      stream: stream,
      status: ManisFunctions.generateStatusNode(Strophe.getResourceFromJid(jid)),
      hands_up: false
    };
    console.log(' video source added : ',that, stream, new Date());
    $(document).trigger('manis.room.someone.joined', [participant]);
  }else {
    setTimeout(function () {
      onRemoteVideoAdded(stream, jid)
    }, 500);
  }

}

/**
 *
 * @param stream
 * @returns {string}
 */
function checkStreamIsScreen(stream,ssrc) {
  if(stream){
    var ssrc = stream.ssrc || null;
    if(ssrc == window.screenSSRC){
      return 'screen';
    }
    return 'video';
  }
  return 'video';
}

/**
 *
 * @param stream
 * @param jid
 */
function onRemoteAudioAdded(stream, jid) {
  if(checkStreamTrackAlive(stream)){
    console.log(' audio source added : ', stream, new Date());
    $(document).trigger('manis.room.someone.audio.added', [{
      info: ManisFunctions.getUserInfoByJid(jid),
      status: ManisFunctions.generateStatusNode(Strophe.getResourceFromJid(jid)),
      stream: stream,
      video_muted: true,
      type: 'audio'
    }]);
  }else {
    setTimeout(function () {
      onRemoteAudioAdded(stream, jid);
    }, 500)
  }
}



