var SDP = require('./strophe.jingle.sdp');
var ManisFunctions = require('../../manis.functions');
var simulcast = require('../../libs/simulcast');
var transform = require('sdp-transform');
var ErrorCode = require('../../errorCode');

/**
 * Base class for ColibriFocus and JingleSession.
 * @param connection Strophe connection object
 * @param sid my session identifier(resource)
 * @constructor
 */
function SessionBase(connection, sid) {
  this.connection = connection;
  this.sid = sid;

  this.screenSender = null;
  this.localVideoSender = null;
  this.localAudioSender = null;
  /**
   * The indicator which determines whether the (local) video has been muted
   * in response to a user command in contrast to an automatic decision made
   * by the application logic.
   */
  this.videoMuteByUser = false;
}


SessionBase.prototype.modifySources = function (successCallback) {
  var self = this;
  if (this.peerconnection) {
    var isNeedAnswer = false;
    if (successCallback) {
      isNeedAnswer = true;
    }
    this.peerconnection.modifySources(function () {
      console.log(' will set local description , sid : ', self.sid);
      $(document).trigger('setLocalDescription.jingle', [self.sid]);
      if (successCallback) {
        successCallback();
      }
    }, isNeedAnswer);
  }

};

SessionBase.prototype.addSource = function (xmlJingle, fromJid) {
  console.log("=============================addsource=======", xmlJingle[0].outerHTML);
  var elem = xmlJingle.find(">content");
  console.log('handle source add content : ', elem, fromJid);

  var self = this;
  // FIXME: dirty waiting
  if (!self.peerconnection.localDescription) {
    console.warn("addSource - localDescription not ready yet")
    setTimeout(function () {
        self.addSource(elem, fromJid);
      },
      200
    );
    return;
  }

  var videoNode = $(elem).find('description[media="video"]') || null;
  if(videoNode.length){
    var videoSource = videoNode.find('source[xmlns="urn:xmpp:jingle:apps:rtp:ssma:0"]')|| null;
    if(videoSource){
      var videoSSRC = videoSource.attr('ssrc') || null;
      var jid = videoSource.attr('jid') || null;
      var isScreen = videoSource.attr('screen') || false;
      if(videoSSRC){
        console.log('handle video source added : ', videoSSRC);
        var removeSSRC = parseInt(videoSSRC);
        ssrc2jid[removeSSRC] = jid;
        ssrc2resolution[removeSSRC] = jid;
        ssrc2videoType[removeSSRC] = jid;
        if(isScreen.toString() == 'true'){
          window.screenSSRC = videoSSRC;
        }
      }
    }
  }

  var audioNode = $(elem).find('description[media="audio"]') || null;
  if(audioNode.length){
    var audioSource = audioNode.find('source[xmlns="urn:xmpp:jingle:apps:rtp:ssma:0"]')|| null;
    if(audioSource){
      var audioSSRC = audioSource.attr('ssrc') || null;
      var jid = videoSource.attr('jid') || null;
      if(audioSSRC){
        console.log('handle audio source added : ', audioSSRC);
        var removeSSRC = parseInt(audioSSRC);
        ssrc2jid[removeSSRC] = jid ;
        ssrc2resolution[removeSSRC] = jid;
        ssrc2videoType[removeSSRC] = jid;
      }
    }
  }

  self.peerconnection.addSource(elem);

  if (navigator.mozGetUserMedia){
    self.modifySources(function (result) {
      console.log('will create answer', result);
    });
  }else {
    self.modifySources();
  }
};

SessionBase.prototype.removeSource = function (elem, fromJid) {

  console.log('handle source remove content : ', elem, fromJid);
  var self = this;
  // FIXME: dirty waiting
  if (!self.peerconnection.localDescription) {
    console.warn("removeSource - localDescription not ready yet")
    setTimeout(function () {
        self.removeSource(elem, fromJid);
      },
      200
    );
    return;
  }
  self.peerconnection.removeSource(elem);

  self.modifySources();


  var videoNode = $(elem).find('description[media="video"]') || null;
  if(videoNode.length){
    var videoSource = videoNode.find('source[xmlns="urn:xmpp:jingle:apps:rtp:ssma:0"]')|| null;
    if(videoSource){
      var videoSSRC = videoSource.attr('ssrc') || null;
      if(videoSSRC){
        console.log('handle video source remove : ', videoSSRC);
        var removeSSRC = parseInt(videoSSRC);
        delete ssrc2jid[removeSSRC];
        delete ssrc2resolution[removeSSRC];
        delete ssrc2videoType[removeSSRC];
        $(document).trigger('mains.on.stream.removed', ['video', videoSSRC, false]);
      }
    }
  }

  var audioNode = $(elem).find('description[media="audio"]') || null;
  if(audioNode.length){
    var audioSource = audioNode.find('source[xmlns="urn:xmpp:jingle:apps:rtp:ssma:0"]')|| null;
    if(audioSource){
      var audioSSRC = audioSource.attr('ssrc') || null;
      if(audioSSRC){
        console.log('handle audio source remove : ', audioSSRC);
        var removeSSRC = parseInt(audioSSRC);
        delete ssrc2jid[removeSSRC];
        delete ssrc2resolution[removeSSRC];
        delete ssrc2videoType[removeSSRC];
        $(document).trigger('mains.on.stream.removed', ['audio', audioSSRC, false]);
      }
    }
  }

};

SessionBase.prototype.getSendersByType = function (replaceType) {
  var that = this;
  var shallRemoveSender = (replaceType == 'video') ? that.peerconnection.localVideoSender : that.peerconnection.localAudioSender;
  if(!shallRemoveSender){
    var currentSenders = that.peerconnection.peerconnection.getSenders();
    for(var i = 0; i < currentSenders.length; i++){
      var sender = currentSenders[i];
      if(sender.track.kind == replaceType && !shallRemoveSender){
        shallRemoveSender = sender;
      }
    }
  }
  return shallRemoveSender;
};

SessionBase.prototype.peerconnectionNegotiate = function (callback) {
  var pc = this.peerconnection.peerconnection;
  pc.setRemoteDescription(pc.remoteDescription, function () {
    console.info('set remote description success ');
    pc.createAnswer(function (modifiedAnswer) {
      console.log('create answer success : ',modifiedAnswer);
      pc.setLocalDescription(modifiedAnswer, function () {
        callback();
      }, function (err) {
        console.error('set local description failed', err);
      });
    }, function (err) {
      console.error('create answer failed', err);
    })
  }, function (err) {
    console.error('====peerconnectionNegotiate==set remote description failed : ', err);
  });
};


SessionBase.prototype.replaceSdpSsrc = function (oldSdp, newSdp, replaceType) {
  console.log('oldSdp : ', '\r\n', transform.parse(oldSdp), '\r\n', replaceType);
  console.log('newSdp : ', '\r\n', transform.parse(newSdp), '\r\n', replaceType);

  var oldSdpObject = transform.parse(oldSdp);
  // record old sdp ssrcs for compare adn replace in new sdp
  var oldSdpSSRCs = [];
  if(oldSdpObject.media && oldSdpObject.media.length){
    oldSdpObject.media.forEach(function (media) {
      if(media.type == replaceType){
        oldSdpSSRCs = media.ssrcs.map(function(ssrcLine){
          return ssrcLine.id;
        })
      }
    })
  }
  console.log('old sdp ', replaceType, ' ssrcs : ', oldSdpSSRCs);

  var newSdpObject = transform.parse(newSdp);

  var newSdpSSRCs = [];
  if(newSdpObject.media && newSdpObject.media.length){
    newSdpObject.media.forEach(function (media) {
      if(media.type == replaceType){
        newSdpSSRCs = media.ssrcs.map(function(ssrcLine){
          return ssrcLine.id;
        })
      }
    });
  }

  console.log('new sdp ', replaceType, ' ssrcs : ', newSdpSSRCs);

  var replaceObject = {};

  for(var i = 0; i < newSdpSSRCs.length; i++){
    var newSSRC = newSdpSSRCs[i];
    for(var j = 0; j < oldSdpSSRCs.length; j++){
      var oldSSRC = oldSdpSSRCs[j];
      if(newSdpSSRCs.indexOf(oldSSRC) == -1 && oldSdpSSRCs.indexOf(newSSRC) == -1){
        replaceObject.from = newSSRC;
        replaceObject.to = oldSSRC;
      }

    }
  }

  console.log('sdp replace to : ', replaceObject);


  if(newSdpObject.media && newSdpObject.media.length){
    newSdpObject.media.forEach(function (media) {
      if(media.type == replaceType){
        var replacedSSRCs = media.ssrcs.map(function(ssrcLine){
          if(ssrcLine.id == replaceObject.from){
            ssrcLine.id = replaceObject.to;
          }
          return ssrcLine;
        });
        media.ssrcs = replacedSSRCs;
      }
    });
  }

  console.log('sdp replaced : ', newSdpObject);

  return transform.write(newSdpObject);

};


/**
 * Switches video streams.
 * @param new_stream new stream that will be used as video of this session.
 * @param oldStream old video stream of this session.
 * @param success_callback callback executed after successful stream switch.
 */
SessionBase.prototype.switchStreams = function (new_stream, oldStream, success_callback) {
  console.log('old stream : ', oldStream, ' new stream : ', new_stream);
  var self = this;

  // Stop the stream to trigger onended event for old stream
  if (oldStream.getAudioTracks().length) {
    oldStream.getAudioTracks().forEach(function (track) {
      track.stop();
    });
  }

  if (oldStream.getVideoTracks().length) {
    oldStream.getVideoTracks().forEach(function (track) {
      track.stop();
    });
  }

  // Remember SDP to figure out added/removed SSRCs
  var oldSdp = null;
  if (self.peerconnection) {
    if (self.peerconnection.localDescription) {
      oldSdp = new SDP(self.peerconnection.localDescription.sdp);
    }
    self.peerconnection.removeStream(oldStream, true);
    self.peerconnection.addStream(new_stream);
  }

  self.connection.jingle.localVideo = new_stream;

  self.connection.jingle.localStreams = [];

  //in firefox we have only one stream object
  if (self.connection.jingle.localAudio != self.connection.jingle.localVideo)
    self.connection.jingle.localStreams.push(self.connection.jingle.localAudio);
  self.connection.jingle.localStreams.push(self.connection.jingle.localVideo);

  // Conference is not active
  if (!oldSdp || !self.peerconnection) {
    success_callback();
    return;
  }

  self.peerconnection.switchstreams = true;
  self.modifySources(function () {
    console.log('modify sources done');

    success_callback();

    var newSdp = new SDP(self.peerconnection.localDescription.sdp);
    console.log("SDPs", oldSdp, newSdp);
    self.notifyMySSRCUpdate(oldSdp, newSdp);
  });

};

SessionBase.prototype.removeScreen = function (oldStream, success_callback) {

  var self = this;
  // Stop the stream to trigger onended event for old stream

  if (oldStream.getAudioTracks().length) {
    oldStream.getAudioTracks().forEach(function (track) {
      track.stop();
    });
  }

  if (oldStream.getVideoTracks().length) {
    oldStream.getVideoTracks().forEach(function (track) {
      track.stop();
    });
  }

  // Remember SDP to figure out added/removed SSRCs
  var oldSdp = null;
  if (self.peerconnection) {
    if (self.peerconnection.localDescription) {
      oldSdp = new SDP(self.peerconnection.localDescription.sdp);
    }
    console.log(' peerconnection ', self.peerconnection, self.peerconnection.peerconnection.removeTrack);
    if(self.peerconnection.peerconnection.removeTrack != undefined){
      self.peerconnection.peerconnection.removeTrack(self.screenSender);
      console.log(' remove screen track', self.screenSender);
    }else {
      self.peerconnection.removeStream(oldStream, true);
    }
  }
  //
  //
  self.connection.jingle.localStreams = [];

  //in firefox we have only one stream object
  if (self.connection.jingle.localAudio != self.connection.jingle.localVideo)
    self.connection.jingle.localStreams.push(self.connection.jingle.localAudio);
  self.connection.jingle.localStreams.push(self.connection.jingle.localVideo);

  // Conference is not active
  if (!oldSdp || !self.peerconnection) {
    connection.jingle.localScreen = null;
    success_callback({
      code: 200,
      msg: 'remove source from connection complete',
      errorCode: ErrorCode.no_error
    });
    return;
  }

  self.peerconnection.switchstreams = true;
  self.modifySources(function () {
    console.log('modify sources done');
    connection.jingle.localScreen = null;
    success_callback({
      code: 200,
      msg: 'remove source from connection complete',
      errorCode: ErrorCode.no_error
    });

    var newSdp = new SDP(self.peerconnection.localDescription.sdp);
    console.log("SDPs", oldSdp, newSdp);
    self.notifyMySSRCUpdate(oldSdp, newSdp);
  });
};


SessionBase.prototype.addScreen = function (screenStream, successCallback) {
  var self = this;

  var oldSdp = null;
  if (self.peerconnection) {
    if (self.peerconnection.localDescription) {
      oldSdp = new SDP(self.peerconnection.localDescription.sdp);
    }
    console.log(' peerconnection ', self.peerconnection, self.peerconnection.peerconnection.addTrack);
    if(self.peerconnection.peerconnection.addTrack != undefined){
      var track = screenStream.getVideoTracks()[0];
      var screenSender = self.peerconnection.peerconnection.addTrack(track, screenStream);
      self.screenSender = screenSender;
      console.log(' add screen track', screenSender);
    }else {
      self.peerconnection.addStream(screenStream);
    }
  }
  self.connection.jingle.localScreen = screenStream;

  self.connection.jingle.localStreams = [];

  //in firefox we have only one stream object
  if (self.connection.jingle.localAudio != self.connection.jingle.localScreen)
    self.connection.jingle.localStreams.push(self.connection.jingle.localScreen);
  self.connection.jingle.localStreams.push(self.connection.jingle.localScreen);

  if (!oldSdp || !self.peerconnection) {
    successCallback({
      code: 402,
      msg: 'Conference is not active',
      errorCode: ErrorCode.conference_room_wait_others
    });
    return;
  }
  self.peerconnection.switchstreams = true;
  self.modifySources(function () {
    console.log('modify sources done');

    successCallback({
      code: 200,
      msg: 'success add screen source !',
      response: screenStream,
      errorCode: ErrorCode.no_error
    });

    var newSdp = new SDP(self.peerconnection.localDescription.sdp);
    console.log("SDPs", oldSdp, newSdp);
    self.notifyMySSRCUpdate(oldSdp, newSdp);
    $(document).trigger('mains.on.screen.share.start');
  });
};

SessionBase.prototype.removeLocalStream = function (removeStreams, success_callback) {

  var self = this;
  var senders = [];
  // Stop the stream to trigger onended event for old stream
  if(removeStreams.length){
    for (var i = 0; i < removeStreams.length; i++) {
      var stream = removeStreams[i];
      if (stream.getAudioTracks().length) {
        stream.getAudioTracks().forEach(function (track) {
          track.stop();
        });
        senders.push(self.localAudioSender);
      }
      if (stream.getVideoTracks().length) {
        stream.getVideoTracks().forEach(function (track) {
          track.stop();
        });
        senders.push(self.localVideoSender);
      }
    }
  }
  // console.warn('Active sends: ', senders, ' local audio sender:', self.localAudioSender, ' local video sender: ', self.localVideoSender);
  // Remember SDP to figure out added/removed SSRCs
  var oldSdp = null;
  if (self.peerconnection) {
    if (self.peerconnection.localDescription) {
      oldSdp = new SDP(self.peerconnection.localDescription.sdp);
    }
    console.log(' peerconnection ', self.peerconnection, self.peerconnection.peerconnection.removeTrack);
    if(self.peerconnection.peerconnection.removeTrack != undefined){
      if(senders){
        for (var j = 0; j < senders.length; j++) {
          var sender = senders[j];
          self.peerconnection.peerconnection.removeTrack(sender);
          console.log(' remove screen track', sender);
        }
        self.localVideoSender = null;
        self.localAudioSender = null;
      }
    }else {
      for (var k = 0; k < removeStreams; k++) {
        var stream = removeStreams[k];
        self.peerconnection.removeStream(stream, true);
      }
    }
  }

  // Conference is not active
  if (!oldSdp || !self.peerconnection) {
    connection.jingle.localScreen = null;
    success_callback({
      code: 200,
      msg: 'remove source from connection complete',
      errorCode: ErrorCode.no_error
    });
    return;
  }

  self.peerconnection.switchstreams = true;
  self.modifySources(function () {
    console.log('modify sources done');
    connection.jingle.localScreen = null;
    success_callback({
      code: 200,
      msg: 'remove source from connection complete',
      errorCode: ErrorCode.no_error
    });

    var newSdp = new SDP(self.peerconnection.localDescription.sdp);
    console.log("SDPs", oldSdp, newSdp);
    self.notifyMySSRCUpdate(oldSdp, newSdp);
  });
};

SessionBase.prototype.addLocalStream = function (localMediaStream, successCallback) {
  var self = this;

  var oldSdp = null;
  if (self.peerconnection) {
    if (self.peerconnection.localDescription) {
      oldSdp = new SDP(self.peerconnection.localDescription.sdp);
    }
    console.log(' peerconnection ', self.peerconnection, self.peerconnection.peerconnection.addTrack);
    if(self.peerconnection.peerconnection.addTrack != undefined){
      // var track = screenStream.getVideoTracks()[0];
      // var screenSender = self.peerconnection.peerconnection.addTrack(track, screenStream);
      // self.screenSender = screenSender;
      // console.log(' add screen track', screenSender);

      if (window.webkitMediaStream) {
        var audioStream = new webkitMediaStream();
        var videoStream = new webkitMediaStream();
        var audioTracks = localMediaStream.getAudioTracks();
        var videoTracks = localMediaStream.getVideoTracks();
        for (var i = 0; i < audioTracks.length; i++) {
          audioStream.addTrack(audioTracks[i]);
        }
        for (i = 0; i < videoTracks.length; i++) {
          videoStream.addTrack(videoTracks[i]);
        }
        connection.jingle.localAudio = audioStream;
        connection.jingle.localVideo = videoStream;
      } else {
        connection.jingle.localAudio = localMediaStream;
        connection.jingle.localVideo = localMediaStream;
      }
      // @Todo add track
      localMediaStream.getTracks().forEach(function (track) {
        var sender = self.peerconnection.peerconnection.addTrack(
          track,
          localMediaStream
        );
        if (sender.track.kind == 'video' && !self.localVideoSender) {
          self.localVideoSender = sender;
        }
        if (sender.track.kind == 'audio' && !self.localAudioSender) {
          self.localAudioSender = sender;
        }
      });
    }else {
      console.warn('add track is undefined, switch to add stream');
      self.peerconnection.addStream(localMediaStream);
    }
  }

  self.connection.jingle.localStreams = [];

  self.connection.jingle.localStreams.push(self.connection.jingle.localAudio);
  self.connection.jingle.localStreams.push(self.connection.jingle.localVideo);

  if (!oldSdp || !self.peerconnection) {
    successCallback({
      code: 402,
      msg: 'Conference is not active',
      errorCode: ErrorCode.conference_room_wait_others
    });
    return;
  }
  self.peerconnection.switchstreams = true;
  self.modifySources(function () {
    console.log('modify sources done');

    successCallback({
      code: 200,
      msg: 'success add screen source !',
      response: localMediaStream,
      errorCode: ErrorCode.no_error
    });

    var newSdp = new SDP(self.peerconnection.localDescription.sdp);
    console.log("SDPs", oldSdp, newSdp);
    self.notifyMySSRCUpdate(oldSdp, newSdp);
    // $(document).trigger('mains.on.screen.share.start');
  });
};

/**
 * Figures out added/removed ssrcs and send update IQs.
 * @param old_sdp SDP object for old description.
 * @param new_sdp SDP object for new description.
 */
SessionBase.prototype.notifyMySSRCUpdate = function (old_sdp, new_sdp) {

  var old_media = old_sdp.getMediaSsrcMap();
  var new_media = new_sdp.getMediaSsrcMap();
  //console.log("old/new medias: ", old_media, new_media);

  var toAdd = old_sdp.getNewMedia(new_sdp);
  var toRemove = new_sdp.getNewMedia(old_sdp);
  //console.log("to add", toAdd);
  //console.log("to remove", toRemove);
  if (Object.keys(toRemove).length > 0) {
    this.sendSSRCUpdate(toRemove, null, false);
  }
  if (Object.keys(toAdd).length > 0) {
    this.sendSSRCUpdate(toAdd, null, true);
  }
  if (Object.keys(toAdd).length > 0 && Object.keys(toRemove).length <= 0) {
    // 增加了新流
    console.log("to add", toAdd);
    var toAddSSRC = null;
    Object.keys(toAdd).forEach(function (index) {
      var element = toAdd[index];
      if (element.ssrcs) {
        Object.keys(element.ssrcs).forEach(function (indexTow) {
          toAddSSRC = indexTow;
        })
      }
    });
    // if (toAddSSRC) {
    //   // start share second stream
    //   setTimeout(function () {
    //     ManisFunctions.sendChangeMainBlockSourceMsg(Strophe.getResourceFromJid(connection.jid), toAddSSRC, function (result) {
    //       console.log('notice set new stream to main complete');
    //     }, true)
    //   }, 1000);
    // }
  }
  if (Object.keys(toAdd).length <= 0 && Object.keys(toRemove).length > 0) {
    // 移除了流
    console.log("to remove", toRemove);
    var toRemoveSSRC = null;
    Object.keys(toRemove).forEach(function (index) {
      var element = toRemove[index];
      if (element.ssrcs) {
        Object.keys(element.ssrcs).forEach(function (indexTow) {
          toRemoveSSRC = indexTow;
        })
      }
    });
    if (toRemoveSSRC) {
      // stop share second stream
      ManisFunctions.sendHardRemoveStreamMsg(Strophe.getResourceFromJid(connection.jid), toRemoveSSRC, function (result) {
        console.log('notice hard remove stream complete', result);
      })
    }
  }
};

