/**
 * 登录到XMPP服务器
 *
 * @param _successCallback
 * @param _errorCallback
 * @param _nickname
 */
function signInXMPP(_successCallback, _errorCallback, _nickname) {
    var openfireHost = 'https://' + window.manisConfg.messageServer + '/http-bind/';
    var connection = new Openfire.Connection(openfireHost, window.manisConfg.endpoint);
    if (!manisConfg.messageServer) {
        _errorCallback({
            code: ManisResponse.code_421,
            msg: ManisResponse.sign_failed,
            errorCode: ErrorCode.message_server_except
        });
        return false;
    }
    if (!manisConfg.mUserId) {
        _errorCallback({
            code: ManisResponse.code_421,
            msg: ManisResponse.sign_failed,
            errorCode: ErrorCode.message_server_user_except
        });
        return false;
    }
    var xmppUser = manisConfg.mUserId.toLowerCase() + '@' + manisConfg.messageServer;
    var xmppPassword = manisConfg.token || "";
    connection.connect(
        xmppUser,
        xmppPassword,
        function (status) {
            // console.info('get sign XMPP server result with status :', status);
            if (status == Strophe.Status.CONNECTED) {
                window.connection = connection;
                if (_nickname) {
                    config.nickname = _nickname;
                    connection.emuc.addDisplayNameToPresence(_nickname);
                }
                connection.emuc.addUserIdToPresence(generateUniqueIdForUser());
                connection.emuc.addConfigRoleToPresence(config.role);
                connection.addHandler(_onmessage, null, 'message', null, null, null);
                connection.addHandler(_onMute, 'http://jitsi.org/jitmeet/audio', 'iq', 'set', null, null);
                connection.addHandler(_onVideoBeSet, 'http://jitsi.org/jitmeet/video', 'iq', 'set', null, null);
                // add feature list
                initFeaturesList();
                var pres = $pres();
                // console.info('send pres : ', pres.tree());
                // connection.send(pres);
                window.self = {
                    nickname: config.nickname ? config.nickname : manisConfg.mUserName,
                    jid: connection.jid,
                    resource: Strophe.getResourceFromJid(connection.jid),
                };
                _successCallback({
                    code: ManisResponse.code_200,
                    msg: ManisResponse.connect_server_success,
                    response: {call_id: connection.jid},
                    errorCode: ErrorCode.no_error
                });
            }
        },
        function (error) {
            _errorCallback({
                code: ManisResponse.code_500,
                msg: ManisResponse.sign_failed,
                errorCode: ErrorCode.message_server_connect_failed
            });
        }
    );
}


function sendPrivateMessage(message, participant, nickname, successCallback, msgType, xms, childNodeType) {
    if (msgType == undefined) {
        msgType = 'chat';
    }
    if (xms == undefined) {
        xms = 'http://jabber.org/protocol/nick';
    }
    if (childNodeType == undefined) {
        childNodeType = 'nick';
    }
    var msg = $msg({to: participant.jid, type: msgType});
    msg.c('body', message).up();
    if (nickname) {
        msg.c(childNodeType, {xmlns: xms}).t(nickname).up();
    }
    // msg.up();
    console.warn('private msg ', msg.tree());
    window.connection.send(msg);
    successCallback({
        code: ManisResponse.code_200,
        msg: ManisResponse.message_send_complete
    });
};

function sendGetInitAnnotationMsg(receiver, urlData, callback) {
    this.sendAnnotationMsg("message", JSON.stringify(urlData), receiver, callback)
}

function sendAnnotationMsg(msgAction, msgData, msgReceiver, callback) {
    var msg = $msg({to: msgReceiver, type: "groupchat"});
    var xms = "http://igniterealtime.org/protocol/annotate";
    msg.c("annotate", {xmlns: xms, action: msgAction, url: msgData}).up();
    window.connection.send(msg);
    callback({
        code: ManisResponse.code_200,
        msg: ManisResponse.message_send_complete,
        errorCode: ErrorCode.no_error
    });
}


function ringUp(participant, roomNumber, successCallback, errorCallback, type, password) {
    try {
        var timestamp = (new Date()).valueOf();
        var receiver = (participant.indexOf('@') > -1) ? participant : (participant + '@' + manisConfg.messageServer);
        var msg = $msg({to: receiver, type: 'chat', from: window.connection.jid});
        msg.c('body', {xmlns: 'http://igniterealtime.org/protocol/ringing'}).up();
        msg.c('call', {
            xmlns: 'http://igniterealtime.org/protocol/ringing',
            type: 'invite',
            roomID: roomNumber,
            resource: roomNumber + '@conference.' + manisConfg.messageServer,
            nickname: self.nickname,
            callType: type,
            meetPassword: password,
            createTime: timestamp
        }).up();
        console.info('1111111111111111111 send ring up', msg.tree());
        window.connection.send(msg);
        successCallback({
            code: ManisResponse.code_200,
            msg: ManisResponse.send_inviste_success,
            errorCode: ErrorCode.no_error
        });
    } catch (e) {
        errorCallback({
            code: ManisResponse.code_400,
            msg: ManisResponse.send_inviste_failed,
            errorCode: ErrorCode.invoke_send_failed
        });
    }
};

