	function callSip(gateway, sip, successCallback, errorCallback, sipRecordRule, recorderUserId, roomPassword) {
	    // try {
	    var iq = $iq({to: gateway, from: window.connection.jid, type: 'set'});
	    var dial = iq.c('dial', {xmlns: 'urn:xmpp:rayo:1', to: sip, from: connection.emuc.roomjid});
	    dial.c('header', {
	        name: 'JvbRoomName',
	        value: window.connection.emuc.roomjid
	    }).up().c('header', {
	        name: 'JvbRoomPassword',
	        value: roomPassword
	    }).up().c('header', {
	        name: 'JvbRoomInfo',
	        value: window.config.title
	    }).up();
	    if (sipRecordRule) {
	        dial.c('header', {
	            name: 'JvbRoomRecorder',
	            value: sipRecordRule
	        }).up();
	        dial.c('header', {
	            name: 'JvbUserNum',
	            value: recorderUserId
	        }).up();
	    }
	    dial = dial.up();
	    console.log('send call sip iq : ', iq.tree());
	    window.connection.sendIQ(iq, function (success) {
	        var ref = $(success).find('ref[xmlns="urn:xmpp:rayo:1"]').attr('uri');
	        if (ref == 200) {
	            successCallback({
	                code: ManisResponse.code_200,
	                msg: ManisResponse.call_sip_success,
	                errorCode: ErrorCode.no_error
	            });
	        } else if (ref == 201) {
	            var recorder = $(success).find('header[name="recordResult"]') || null;
	            if (recorder) {
	                var recorderValue = recorder.attr('value') || null;
	                if (recorderValue) {
	                    successCallback({
	                        code: ManisResponse.code_200,
	                        msg: ManisResponse.call_sip_success,
	                        errorCode: ErrorCode.no_error
	                    })
	                }
	            }
	        } else if (ref == 400) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_aready_in,
	                errorCode: ErrorCode.sip_call_in_progress
	            });
	        } else if (ref == 401) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_failed_401,
	                errorCode: ErrorCode.sip_call_failed_error_password
	            });
	        } else if (ref == 413) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_failed_413,
	                errorCode: ErrorCode.sip_call_signalling_error
	            });
	        } else if (ref == 488) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_failed_488,
	                errorCode: ErrorCode.sip_call_hang_up
	            });
	        }  else if (ref == 407) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_failed_407,
	                errorCode: ErrorCode.sip_call_send_failed_over_load
	            });
	        }  else if (ref == 408) {
	            errorCallback({
	                code: ref,
	                msg: ManisResponse.call_sip_failed_408,
	                errorCode: ErrorCode.sip_call_send_failed_disk_full
	            });
	        } else {
	            errorCallback({
	                code: ManisResponse.code_400,
	                msg: ManisResponse.call_sip_failed_500,
	                errorCode: ErrorCode.sip_call_unknown_error
	            });
	        }
	    }, function (error) {
	        errorCallback({
	            code: ManisResponse.code_400,
	            msg: ManisResponse.call_sip_failed,
	            errorCode: ErrorCode.sip_call_send_failed
	        });
	    });
	    // } catch (e) {
	    //   errorCallback({
	    //     code: ManisResponse.code_400,
	    //     msg: ManisResponse.call_sip_failed
	    //   });
	    // }
	};
	
	
