	function _onmessage(msg) {
	    // console.log('------------------------------------'+msg.outerHTML);
	    var from = msg.getAttribute('from');
	    var call = $(msg).find('>call[xmlns="http://igniterealtime.org/protocol/ringing"]');
	
	    var screen = $(msg).find('>screen[xmlns="http://jitsi.org/protocol/screen"]');
	    if (screen.length) {
	        var screenStatus = $(msg).find('property').attr('value');
	        console.log('sssssssssssssssssssssss1111111111111', screenStatus);
	        // $(document).trigger('manis.been.stop.screen.share');
	        return true;
	    }
	
	    var handsUp = $(msg).find('>body[xmlns="http://igniterealtime.org/protocol/handsUp"]');
	    if (handsUp.length) {
	        console.log(' handle hands up msg : ', msg, handsUp);
	        var handsUpType = handsUp[0].getAttribute('type') || '';
	        var handsUpNickname = handsUp[0].getAttribute('nickname') || '';
	        var handsUpFrom = handsUp.text() || '';
	        if (handsUpType == 'apply') {
	            $(document).trigger('manis.on.hands.up.msg', [handsUpFrom, handsUpNickname, handsUpType]);
	        } else if (handsUpType == 'agree' || handsUpType == 'refuse') {
	            $(document).trigger('manis.on.hands.up.response', [handsUpFrom, handsUpType]);
	        } else {
	            console.error('unknown hands up msg', handsUpFrom, handsUpType, handsUpNickname);
	        }
	        return true;
	    }
	
	    var changeNickname = $(msg).find('>body[xmlns="http://igniterealtime.org/protocol/changeNickname"]');
	    if (changeNickname.length) {
	        var newNickname = changeNickname.text();
	        console.log('moderator order me change my nickname to : ', newNickname);
	        connection.emuc.addDisplayNameToPresence(newNickname);
	        connection.emuc.sendPresence();
	        return true;
	    }
	
	    if (call != undefined && call.length > 0) {
	        var type = call[0].getAttribute('type');
	        if (type == 'invite') {
	            var room = call[0].getAttribute('roomID');
	            var nick = call[0].getAttribute('nickname');
	            var password = call[0].getAttribute('meetPassword');
	            var delay = $(msg).find('>delay[xmlns="urn:xmpp:delay"]');
	            var callType = call[0].getAttribute('callType') || '0';
	            console.info('22222222222222222222222 on ring up', msg);
	            if (delay.length) {
	                return;
	            } else {
	                $(document).trigger('oninvite.muc', [from, nick, room, password, callType]);
	            }
	        } else if (type == 'accept') {
	            var room = call[0].getAttribute('roomID');
	            var nick = call[0].getAttribute('nickname');
	            console.info('5555555555555555555555 on ring up accept ', msg);
	            $(document).trigger('manis.onringup.reply', [from, nick, room, 'accept']);
	        } else if (type == 'reject') {
	            var room = call[0].getAttribute('roomID');
	            var nick = call[0].getAttribute('nickname');
	            console.info('666666666666666666666 on ring up reject ', msg);
	            var isCaller = call[0].getAttribute('isCaller') || '0';
	            if (isCaller == '1') {
	                // call cancel by caller
	                window.incomingCall = false;
	                $(document).trigger('manis.onringup.reply', [from, nick, room, 'cancel']);
	            } else {
	                // call reject by receiver
	                $(document).trigger('manis.onringup.reply', [from, nick, room, 'reject']);
	            }
	        } else if (type == 'busy') {
	            var room = call[0].getAttribute('roomID');
	            var nick = call[0].getAttribute('nickname');
	            console.info('999999999999999999999 on ring up busy ', msg);
	            $(document).trigger('manis.onringup.reply', [from, nick, room, 'busy']);
	        } else if (type == 'join') {
	            var room = call[0].getAttribute('roomID');
	            var password = call[0].getAttribute('meetPassword');
	            console.log('zzzzzzzzzzzzzzzzzzzz on room complete ', msg);
	            $(document).trigger('oninvite.room.complete.after.accept.request', [room, password]);
	        } else if (type == 'processed') {
	            console.log('xxxxxxxxxxxxxxxxxxxxxx on ring call was processed ', msg);
	            $(document).trigger('oninvite.room.processed');
	        }
	    }
	    var msgType = msg.getAttribute('type');
	    if (msgType && msgType.length && msgType == 'chat') {
	        var txt = $(msg).find('>body').text();
	        var resolutionMsg = $(msg).find('>resolution[xmlns="http://jabber.org/protocol/resolution"]');
	        if (resolutionMsg.length) {
	            var resolutionOperate = resolutionMsg.text();
	            var targetResolution = txt;
	            // console.log(from, 'order me ', resolutionOperate, 'to ', targetResolution);
	            if (resolutionOperate == 'orderChangeResolution') {
	                $(document).trigger('manis.on.change.resolution.request', [from, targetResolution]);
	                return true;
	            }
	            return true;
	        }
	        var nick = $(msg).find('>nick[xmlns="http://jabber.org/protocol/nick"]').text();
	        var privateMsg = true;
	        if (txt && nick) {
	            $(document).trigger('onmessage.muc', [from, nick, txt, privateMsg, 'yourself']);
	        }
	    }
	    var grantAdmin = $(msg).find('>body[xmlns="http://igniterealtime.org/protocol/grantAdmin"]');
	    if (grantAdmin.length) {
	        onGrantAdmin(msg);
	    }
	    var ejectUser = $(msg).find('>body[xmlns="http://igniterealtime.org/protocol/eject"]');
	    if (ejectUser.length) {
	        onEject(msg);
	    }
	    return true;
	}
	
