var Status = require('./manis.status');
var Functions = require('./manis.functions');

var ManisManagement = {

  onMsg: null,

  onMsgTime: null,

  allMsg: 0,

  audioRetryTimes: 10,//times, first enter try do audio mute or not

  videoRetryTimes: 10,//times, first enter try do video close or not

  managerSetAudioMuteTry: null,

  managerSetVideoMuteTry: null,

  handleManageMsg: function (msg) {

    console.log('handle manage message from administrator: ', msg);

    var systemUpgrade = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/upgrade"]');
    if(systemUpgrade.length){
      //@Comment upcoming, inprogress, complete
      var section = systemUpgrade.text() || 'upcoming';
      $(document).trigger('manis.on.system.upgrade', [section]);
      return true;
    }

    // all audio control
    var allAudioControl = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/allAudio"]');
    if(allAudioControl.length){
      var allAudioMute = allAudioControl.text() == 'true';
      this.setAllAudioMute(allAudioMute);
      return true;
    }

    // all video control
    var allVideoControl = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/allVideo"]');
    if(allVideoControl.length){
      var allVideoMute = allVideoControl.text() == 'true';
      this.setAllVideoMute(allVideoMute);
      return true;
    }

    // audio control someone
    var singleAudioControl = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/audio"]');
    if(singleAudioControl.length){
      var singleAudioMute = singleAudioControl.text() == 'true';
      this.managerSetAudioMute(singleAudioMute);
      return true;
    }

    // video control someone
    var singleAudioControl = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/video"]');
    if(singleAudioControl.length){
      var singleAudioMute = singleAudioControl.text() == 'true';
      this.managerSetVideoMute(singleAudioMute);
      return true;
    }

    // room will close
    var roomWillClose = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/toClose"]');
    if(roomWillClose.length){
      var timeLeft = roomWillClose.attr('time-left') || 0;
      $(document).trigger('manis.on.room.countdown', [timeLeft]);
      return true;
    }

    // room moderator lost
    var moderatorLost = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/lostModerator"]');
    if(moderatorLost.length){
      var moderatorLostStatus = moderatorLost.text() == 'true';
      var timeLeft = moderatorLost.attr('time-left') || null;
      $(document).trigger('manis.on.with.moderator.changed',[!moderatorLostStatus, timeLeft]);
      return true;
    }

    // room lock
    var roomLock = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/lock"]');
    if(roomLock.length){
      var lockStatus = roomLock.text() == 'true';
      if (Functions.isAdmin()) {
        this.manageLockRoom(lockStatus);
      }else {
        $(document).trigger('manis.room.lock.status.change', [lockStatus]);
      }
      return true;
    }

    // handle text message
    var groupChat = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/groupChat"]');
    if(groupChat.length){
      textContent = groupChat.text() || '';
      this.onManagementMsg(textContent, null);
      return true;
    }

    var privateChat = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/privateChat"]');
    if(privateChat.length){
      textContent = privateChat.text() || '';
      this.onManagementMsg(textContent, null);
      return true;
    }

    var moderatorModify = $(msg).find('>conferenceCtrl[xmlns="http://jitsi.org/jitmeet/moderator"]');
    if(moderatorModify.length){
      var moderatorSwitch = moderatorModify.attr('moderator');
      var jid = moderatorModify.text() || '';
      if(jid){
        if(jid == connection.jid){
          console.log('manager change my role : ', moderatorSwitch, ' at : ', new Date());
          var isAdmin = moderatorSwitch == 'true';
          Functions.setAdmin(isAdmin);
          connection.emuc.addConfigRoleToPresence(config.role);
          connection.emuc.addIsModeratorToPresence(Functions.isAdmin());
          connection.emuc.sendPresence();
          if(isAdmin){
            // on set to moderator by administrator
            $(document).trigger('manis.on.grant.admin');
          }else {
            // on remove moderator by administrator
            $(document).trigger('manis.on.remove.admin');
          }
        }else {
          // jid != connection.jis  && I am moderator, reset config.role to 'NONE'
          if(Functions.isAdmin()){
            console.log('manager change moderator from: ', connection.jid, ' to :', jid, ' at: ', new Date());
            Functions.setAdmin(false);
            connection.emuc.addConfigRoleToPresence(config.role);
            connection.emuc.addIsModeratorToPresence(Functions.isAdmin());
            connection.emuc.sendPresence();
            $(document).trigger('manis.on.remove.admin');
          }
        }
      }
    }

    return true;
  },

  // 接收到会控消息
  onManagementMsg: function (msg, nick) {
    this.textMsgFromManager(msg, nick);
  },

  /**
   * 文本消息
   * @param message
   * @param nick
   * @returns {boolean}
   */
  textMsgFromManager: function (message, nick) {
      var lang = window.localStorage.getItem('defaultLanguage') || 'zh';
      if(lang == 'en'){
        var sender = 'Manager';
      }else {
        var sender = '会控管理员';
      }
      $(document).trigger('onmessage.muc', [config.focusUserJid, nick ? nick : sender, message]);
      return true;
  },

  // 全员静音
  setAllAudioMute: function (muteStatus) {
    // ignore moderator while set all
    if((config.role == 'CRUA' || config.role == 'CRUO') && muteStatus ){
      return true;
    }
    this.managerSetAudioMute(muteStatus);
  },

  // 全员禁视频
  setAllVideoMute: function (muteStatus) {
    // ignore moderator while set all
    if((config.role == 'CRUA' || config.role == 'CRUO') && muteStatus ){
      return true;
    }
    this.managerSetVideoMute(muteStatus);
  },

  // 锁定会议室
  manageLockRoom: function (isLockRoom) {
    if (isLockRoom == window.lock) {
      console.log('now room is : ', isLockRoom, 'do nothing');
      $(document).trigger('manis.room.lock.status.change', [isLockRoom]);
      return true;
    }
    try {
      var username = manisConfg.mUserId;
      var roomNumber = roomName.split('@')[0];
      var _header = Functions.authoredHeader();
      $.ajax({
        url: window.serverPath + "/conference/lock?i=" + username + "&r=" + roomNumber + "&l=" + isLockRoom,
        headers: _header,
        // dataType: 'jsonp',
        dataType: 'json',
        processData: false,
        type: 'get',
        success: function (rs) {
          if (rs.code == 200) {
            window.lock = isLockRoom;
            console.info('lock or unlock room by management success, now room lock status : ', isLockRoom);
            $(document).trigger('manis.room.lock.status.change', [isLockRoom]);
          }
        }
      });
    } catch (e) {
      console.error('lock or unlock room by management failed, now room lock status : ', !isLockRoom);
    }
  },

  // 结束会议
  hangup: function () {
    $(document).trigger('manis.management.kill', [true]);
  },

  // 开关声音
  managerSetAudioMute: function (mute) {
      // if (window.participants.length < 2) {
      //     console.warn('other participant not join yet');
      //     if(this.audioRetryTimes > 0) {
      //         this.managerSetAudioMuteTry = setTimeout(function () {
      //             clearTimeout(ManisManagement.managerSetAudioMuteTry);
      //             ManisManagement.audioRetryTimes--;
      //             ManisManagement.managerSetAudioMute(mute);
      //         }, 2000);
      //     }
      //     return true;
      // }
      // this.audioRetryTimes = 0;

      Functions.setAudioMute(mute, function(){
          window.forceMuted = mute;
          $(document).trigger('onmute.audio.muc', [mute]);
          // $(document).trigger('audiomuted.muc', [connection.jid, mute.toString()]);
      }, function (e) {
          console.error('managerSetAudioMute ', e);
      });
    return true;
  },

  // 开关视频
  managerSetVideoMute: function (mute) {
    // if (window.participants.length < 2) {
    //   console.warn('other participant not join yet');
    //     if(this.videoRetryTimes > 0) {
    //         this.managerSetVideoMuteTry = setTimeout(function () {
    //             clearTimeout(ManisManagement.managerSetVideoMuteTry);
    //             ManisManagement.videoRetryTimes--;
    //             ManisManagement.managerSetVideoMute(mute);
    //         }, 2000);
    //     }
    //   return true;
    // }
    // this.videoRetryTimes = 0;

    Functions.setVideoMute(mute, function () {
      // $(document).trigger('videomuted.muc', [connection.jid, mute]);
      $(document).trigger('onmute.video.muc', [mute]);
    }, function (error) {
      console.error('manage set video mute switch failed ', error);
    });

  },
};

module.exports = ManisManagement;


