  /**
   * 离开会议室
   * @param callback
   */
  leaveConference: function (callback) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    if (window.connection == undefined) {
      callback({
        code: ManisResponse.code_401,
        msg: ManisResponse.connection_except,
        errorCode: ErrorCode.mss_connection_except
      });
      return;
    }
    if (!ManisFunctions.isInRoom()) {
      callback({
        code: ManisResponse.code_407,
        msg: ManisResponse.you_are_not_in_room,
        errorCode: ErrorCode.conference_room_not_participate
      });
      return;
    }
    // try {
      Status.disposeConference();
      // connection.emuc.doLeave();
      ManisConference.leaveConferenceRoom();
      window.participants = [];
      window.mutedAudios = {};
      window.localSSRCS = [];
      Status.currentTargetResolution = null;
      Status.currentTargetVideoInput = null;
      window.currentVideoInputDeviceId = null;
      window.currentAudioInputDeviceId = null;
      window.currentAudioOutputDeviceId = null;
      window.incomingCall = false;
      var localAudio = connection.jingle.localAudio;
      var localVideo = connection.jingle.localVideo;
      var localScreen = connection.jingle.localScreen;
      if (localAudio && localAudio.getTracks().length) {
        localAudio.getTracks().forEach(function (track) {
          track.stop();
        });
      }
      if (localVideo && localVideo.getTracks().length) {
        localVideo.getTracks().forEach(function (track) {
          track.stop();
        });
      }
      if (localScreen && localScreen.getTracks().length) {
        localScreen.getTracks().forEach(function (track) {
          track.stop();
        });
      }
      callback({
        code: ManisResponse.code_200,
        msg: ManisResponse.leave_room,
        errorCode: ErrorCode.no_error
      });
    // } catch (e) {
    //   callback(LICENSE_ERROR);
    // }
  },
