  /**
   *  捕获音频开关变更
   * @param callback
   */
  onAudioToggleMute: function (callback) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    $(document).unbind('audiomuted.muc');
    $(document).bind('audiomuted.muc', function (event, jid, isMuted) {
      isMuted = (isMuted == 'true');
      for (var i = 0; i < window.participants.length; i++) {
        var participant = window.participants[i];
        if (participant.resource == Strophe.getResourceFromJid(jid)) {
          participant.audio_muted = isMuted;
          window.participants[i] = participant;
        }
      }
      if (mutedAudios[jid] !== isMuted) {
        mutedAudios[jid] = isMuted;
        callback({
          user: ManisFunctions.getUserInfoByJid(jid),
          mute: isMuted,
          errorCode: ErrorCode.no_error
        });
      }
    });
  },
