  /**
   * 改变视频输入列表
   * @param inputDeviceId
   * @param callback
   * @param videoResolution
   */
  changeVideoInputDevice: function (inputDeviceId, callback, videoResolution) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    var targetResolution = videoResolution ? videoResolution : config.currentResolution;
    if (!inputDeviceId && !targetResolution) {
      callback({
        code: ManisResponse.code_421,
        msg: ManisResponse.video_device_id_except,
        errorCode: ErrorCode.change_video_input_device_except
      });
    }
    window.currentVideoInputDeviceId = inputDeviceId;
    config.resolution = config.storeResolution;
    Status.modifyLocalVideoStream(targetResolution, inputDeviceId, callback);
  },

  /**
   * 修改分辨率
   * @param resolution
   * @param callback
   */
  changeResolution: function (resolution, callback) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    if (window.connection == undefined) {
      callback({
        code: ManisResponse.code_401,
        msg: ManisResponse.connection_except,
        errorCode: ErrorCode.mss_connection_except
      });
      return;
    }
    if (!ManisFunctions.isInRoom()) {
      callback({
        code: ManisResponse.code_407,
        msg: ManisResponse.you_are_not_in_room,
        errorCode: ErrorCode.conference_room_not_participate
      });
      return;
    }
    if (!resolution || (AcceptResolutions.indexOf(resolution) < 0)) {
      callback({
        code: ManisResponse.code_421,
        msg: ManisResponse.resolution_params_error,
        error: resolution,
        errorCode: ErrorCode.change_resolution_not_match
      });
      return;
    }
    try{
      Status.changeResolutionByApplyConstraints(resolution, window.currentVideoInputDeviceId, callback);
    }catch (e){
      // console.error(e);
      Status.modifyLocalVideoStream(resolution, window.currentVideoInputDeviceId, callback);
    }
  },

