  /**
   * 屏幕分享(双流模式)
   * @param callback
   */
  addScreenSourceToConnection: function (callback) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    if (window.connection == undefined) {
      callback({
        code: ManisResponse.code_401,
        msg: ManisResponse.connection_except,
        errorCode: ErrorCode.mss_connection_except
      });
      return;
    }
    if (!ManisFunctions.isInRoom()) {
      callback({
        code: ManisResponse.code_407,
        msg: ManisResponse.you_are_not_in_room,
        errorCode: ErrorCode.conference_room_not_participate
      });
      return;
    }
    if (!ManisFunctions.isAdmin()) {
      callback({
        code: ManisResponse.code_403,
        msg: ManisResponse.out_of_authority,
        errorCode: ErrorCode.without_permission_for_the_operation
      });
      return;
    }
    var session = window.activecall;
    if (!session) {
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.share_screen_failed,
        errorCode: ErrorCode.conference_room_wait_others
      });
      return;
    }
    // 支持不用插件直接分享的浏览器直接调用获取屏幕分享方法,插件方式兼容旧版本浏览器
    if (navigator.getDisplayMedia || navigator.mediaDevices.getDisplayMedia) {
      this.screenCaptureWithoutPlugin(function (stream) {
          window.isUsingScreenStream = true;
          Status.addScreenSourceToConnection(stream, callback);
      });
      return;
    }
    if(!ManisScreenShare.isDesktopSharingEnabled()){
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.share_screen_failed,
        errorCode: ErrorCode.share_screen_nonsupport
      });
      return ;
    }
    ManisScreenShare.toggleScreenSharing(function (result) {
      console.log('BBBBBBBBBBBBBBBB', result);
      if (result.response.stream) {
        // get screen stream success
        window.isUsingScreenStream = true;
        Status.addScreenSourceToConnection(result.response.stream, callback);
      } else {
        // get screen stream failed
        callback({
          code: ManisResponse.code_400,
          msg: ManisResponse.share_screen_failed,
          errorCode: result.errorCode
        })
      }
    }, function (error) {
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.share_screen_failed,
        errorCode: error.errorCode
       })
    });
  },

  /**
   * 无插件分享屏幕
   * @param cb
   */
  screenCaptureWithoutPlugin(cb) {
    var _this = this
    _this._startScreenCapture().then(function (stream) {
        stream.addEventListener('inactive', e => {
            console.log('Capture stream inactive - stop recording!', e);
            _this.removeScreenSourceFromConnection(function () {
                $(document).trigger('mains.on.screen.share.end');
            });
        });
        if (typeof cb === 'function') {
          cb(stream);
        }
    });
  },

  /**
   * 无插件获取屏幕流
   * @returns {*}
   * @private
   */
  _startScreenCapture: function() {
    if (navigator.getDisplayMedia) {
        return navigator.getDisplayMedia({video: true});
    } else if (navigator.mediaDevices.getDisplayMedia) {
        return navigator.mediaDevices.getDisplayMedia({video: true});
    } else {
        return navigator.mediaDevices.getUserMedia({video: {mediaSource: 'screen'}});
    }
  },

  /**
   * 从连接中移除屏幕分享流
   * @param callback
   */
  removeScreenSourceFromConnection: function (callback) {
    if (typeof(callback) != 'function') {
      console.error({
        code: ManisResponse.code_421,
        msg: ManisResponse.callback_function_except,
        errorCode: ErrorCode.callback_except
      });
      return;
    }
    if (window.connection == undefined) {
      callback({
        code: ManisResponse.code_401,
        msg: ManisResponse.connection_except,
        errorCode: ErrorCode.mss_connection_except
      });
      return;
    }
    if (!ManisFunctions.isInRoom()) {
      callback({
        code: ManisResponse.code_407,
        msg: ManisResponse.you_are_not_in_room,
        errorCode: ErrorCode.conference_room_not_participate
      });
      return;
    }
    try {
      // console.log('++++++++++++++++++++++++ screen stream ended 0000000000');
      window.removeScreenInprogress = true;
      window.isUsingScreenStream = false;
      Status.removeScreenSourceFromConnection(function (res) {
          ManisScreenShare.streamSwitchDone();
          callback(res)
      });
    } catch (e) {
      callback({
        code: ManisResponse.code_400,
        msg: ManisResponse.share_screen_error,
        errorCode: ErrorCode.share_screen_stop_failed
      })
    }
  },

