  /**
   * 仅发送方式加入到房间(无线投屏)
   * @param room 房间号
   * @param password 房间密码
   * @param nickname 参会昵称
   * @param mediaStream 发送的流媒体(音视频)
   * @param callback 回调函数
   */
  sendOnlyParticipant: function (room, password, nickname, mediaStream, callback) {
      if (typeof(callback) != 'function') {
          console.error({
              code: ManisResponse.code_421,
              msg: ManisResponse.callback_function_except,
              errorCode: ErrorCode.callback_except,
          });
          return;
      }
      if(!room){
          callback({
              code: ManisResponse.code_421,
              msg: ManisResponse.room_number_except,
              errorCode: ErrorCode.conference_room_except
          });
          return;
      }
      if(!mediaStream){
          callback({
              code: ManisResponse.code_421,
              msg: ManisResponse.media_stream_except,
              errorCode: ErrorCode.send_only_stream_except
          });
          return;
      }
      var _this = this;
      window.sendOnly = true;
      var server = DEFAULT_PROTOCOL + this.manisParams.server + JOIN_BY_ANONYMOUS_URL;
      ManisSignIn.signInMeetingCtlByAnonymous(server, nickname, function () {
          _this.keepAlive();
          if (window.webkitMediaStream) {
              var audioStream = new webkitMediaStream();
              var videoStream = new webkitMediaStream();
              var audioTracks = mediaStream.getAudioTracks();
              var videoTracks = mediaStream.getVideoTracks();
              for (var i = 0; i < audioTracks.length; i++) {
                  audioStream.addTrack(audioTracks[i]);
              }
              for (i = 0; i < videoTracks.length; i++) {
                  videoStream.addTrack(videoTracks[i]);
              }
              connection.jingle.localAudio = audioStream;
              connection.jingle.localVideo = videoStream;
          } else {
              connection.jingle.localAudio = mediaStream;
              connection.jingle.localVideo = mediaStream;
          }
          ManisConference.joinConferenceWithMedia(room, password, mediaStream, callback);
      }, function () {
          _this.sendOnlyParticipant(room, password, nickname, mediaStream, callback);
      });
  },

