/**
 * 初始化默认输入设备
 */
function initDefaultInputDevices(audioInput, videoInput, audioOut) {
  if (audioInput) {
    window.currentAudioInputDeviceId = audioInput;
  }
  if (videoInput) {
    window.currentVideoInputDeviceId = videoInput;
  }
  if (audioOut) {
    window.currentAudioOutputDeviceId = audioInput;
  }
  navigator.mediaDevices.enumerateDevices().then(gotInputDevices).catch(handleError);
}

/**
 *
 * @param error
 */
function handleError(error) {
  console.log('navigator.getUserMedia error: ', error);
}

/**
 *
 * @param devicesInfo
 * @returns {*}
 */
function gotInputDevices(devicesInfo) {
  for (var i = 0; i !== devicesInfo.length; ++i) {
    var deviceInfo = devicesInfo[i];
    if (config.debug == true) {
      console.log(deviceInfo);
    }
    if (deviceInfo.kind == 'videoinput' && !window.currentVideoInputDeviceId) {
      window.currentVideoInputDeviceId = deviceInfo.deviceId;
    } else if (deviceInfo.kind == 'audioinput' && !window.currentAudioInputDeviceId) {
      window.currentAudioInputDeviceId = deviceInfo.deviceId;
    }
  }
}


function generateDefaultConstraints(audioOnly) {
  var constraints = {
    audio: {},
    video: {},
  };
  if (audioOnly == true) {
    constraints.video = false;
  } else {
    if (config.resolution) {
      var defaultResolution = config.resolution;
      switch (defaultResolution) {
        case 1080:
        case '1080':
          constraints.video = FullHDConstraints;
          config.videoBandwidth = (config.videoBandwidth == 1024 * 2 * 2) ? config.videoBandwidth : 1024 * 2;
          break;
        case 720:
        case '720':
          constraints.video = HDConstraints;
          config.videoBandwidth = (config.videoBandwidth == 512 * 2 * 2) ? config.videoBandwidth : 512 * 2;
          break;
        case 768:
        case '768':
          constraints.video = XGAConstraints_768;
          config.videoBandwidth = (config.videoBandwidth == 512 * 2 * 2) ? config.videoBandwidth : 512 * 2;
          break;
        case 540:
        case '540':
          constraints.video = VGAConstraints;
          config.videoBandwidth = (config.videoBandwidth == 256 * 2 * 2) ? config.videoBandwidth : 256 * 2;
          break;
        case 480:
        case '480':
          constraints.video = XGAConstraints_480;
          config.videoBandwidth = (config.videoBandwidth == 256 * 2 * 2) ? config.videoBandwidth : 256 * 2;
          break;
        case 240:
        case '240':
          constraints.video = XGAConstraints_240;
          config.videoBandwidth = (config.videoBandwidth == 128 * 2 * 2) ? config.videoBandwidth : 128 * 2;
          break;
        case 180:
        case '180':
          constraints.video = QVGAConstraints;
          config.videoBandwidth = (config.videoBandwidth == 128 * 2 * 2) ? config.videoBandwidth : 128 * 2;
          break;
        default:
          constraints.video = HDConstraints;
          config.videoBandwidth = (config.videoBandwidth == 512 * 2 * 2) ? config.videoBandwidth : 512 * 2;
      }
    }
    if (constraints.video && window.currentVideoInputDeviceId) {
      // constraints.video.deviceId = window.currentVideoInputDeviceId;
      constraints.video.deviceId = window.currentVideoInputDeviceId ? {exact: window.currentVideoInputDeviceId} : undefined;
    }
  }
  if (window.currentAudioInputDeviceId) {
    // constraints.audio.deviceId = window.currentAudioInputDeviceId;
    constraints.audio.deviceId = window.currentAudioInputDeviceId ? {exact: window.currentAudioInputDeviceId} : undefined;
  }
  window.constraints = constraints;
  return constraints;
}

