function validRoom(roomName, roomPassword, successCallBack, errorCallback) {
  var _header = ManisFunctions.authoredHeader();
  try {
    $.ajax({
      url: window.serverPath + JOIN_ROOM_URL,
      // dataType: 'jsonp',
      // jsonpCallback: 'validRoomCallBack',
      data: {r: roomName, p: roomPassword, u: manisConfg.mUserId, j: connection.jid},
      dataType: 'json',
      headers: _header,
      type: 'GET',
      // async: true,
      timeout: 2000,
      success: function (response, textStatus, xOptions) {
        if (xOptions.readyState == 4 && xOptions.status == 200) {
          if (response.code == 200) {
            console.log('success join room :', roomName, response);
            var isModerator = (response.obj.moderator ? response.obj.moderator == connection.jid : ManisFunctions.isAdmin());
            if (response.obj.moderator != undefined) {
              connection.emuc.addIsModeratorToPresence(isModerator);
              connection.emuc.sendPresence();
            }
            // ③ 加入会议
            window.roomName = roomName + '@conference.' + manisConfg.messageServer;
            var resp = response;
            resp.r = roomName;
            resp.vb = response.obj.vb;
            config.gw = response.obj.gw;
            config.title = response.obj.title;
            ManisFunctions.setAdmin(isModerator);
            joinRoom(resp, successCallBack, errorCallback);
          } else if (response.code == 401) {
            // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
            errorCallback({
              code: ManisResponse.code_422,
              msg: ManisResponse.room_password_expect
            });
          } else if (response.code == 403) {
            // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
            errorCallback({
              code: ManisResponse.code_405,
              msg: ManisResponse.room_locked
            });
          }
        } else {
          // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
          errorCallback({
            code: ManisResponse.code_500,
            msg: ManisResponse.join_room_failed
          });
        }
      },
      error: function (xOptions, textStatus) {
        // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
        errorCallback({
          code: ManisResponse.code_500,
          msg: ManisResponse.join_room_failed
        });
      }
    });
  } catch (e) {
    // console.error('xhr request failure with error : ', e);
    errorCallBack({
      code: ManisResponse.code_500,
      msg: ManisResponse.join_room_failed
    });
  }
}

