function join(conferenceName, conferencePassword, successCallback, errorCallback) {
  // ① 判断是否超出会议资源限制
  var _header = ManisFunctions.authoredHeader();
  try {
    $.ajax({
      url: window.serverPath + CHECK_RESOURCES_URL,
      // dataType: 'jsonp',
      // jsonpCallback: 'checkResourcesCallBack',
      data: {r: conferenceName, j: connection.jid, u: manisConfg.mUserId},
      dataType: 'json',
      headers: _header,
      type: 'GET',
      // async: true,
      timeout: 2000,
      success: function (response, textStatus, xOptions) {
        if (xOptions.readyState == 4 && xOptions.status == 200) {
          // ② 判断会议室是否存在以及会议室密码是否正确
          if (response.code == 200) {
            // console.warn('get check resource result: ', response);
            var d = conferencePassword;
            if (d != undefined && d != '' && d != null) {
              console.log('room resources check success.');
              var md5 = crypto.createHash('md5');
              md5.update(conferencePassword);
              d = md5.digest('hex');
            }
            validRoom(conferenceName, d, successCallback, errorCallback);
          } else {
            // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
            errorCallback({
              code: ManisResponse.code_403,
              msg: ManisResponse.out_of_source_limit
            });
          }
        } else {
          // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
          errorCallback({
            code: ManisResponse.code_500,
            msg: ManisResponse.join_room_failed
          });
        }
      },
      error: function (xOptions, textStatus) {
        // console.error('xOptions : ', xOptions, 'textStatus : ', textStatus);
        errorCallback({
          code: ManisResponse.code_500,
          msg: ManisResponse.join_room_failed
        });
      }
    });
  } catch (e) {
    // console.error('xhr request failure with error : ', e);
    errorCallback({
      code: ManisResponse.code_500,
      msg: ManisResponse.join_room_failed
    });
  }
}

