/**
 * 呼叫录播之前获取最近的gw配置
 * @param callback
 */
function updateGWConfig(callback){
  var _header = ManisFunctions.authoredHeader();
  $.ajax({
    url: window.serverPath + CONFIG_RESOURCE_UPDATE,
    data: {roomNumber: config.r, userJid: connection.jid},
    dataType: 'json',
    headers: _header,
    type: 'GET',
    timeout: 2000,
    success: function (response) {
      console.log('update gw config resource', response);
      if(response.code == 200){
        var newGW = response.obj.gw ? response.obj.gw : config.gw;
        callback({
          code: response.mcode,
          response: newGW
        });
      }else {
        console.error('update gw config resource failed: ', response);
        callback({
          code: response.mcode,
          response: config.gw
        });
      }
    },
    error: function (xOptions, textStatus) {
      if (config.debug == true) {
        console.error('update gw config resource failed: ', xOptions, textStatus);
        callback({
          code: 500,
          response: config.gw
        });
      }
    }
  })
}

