var ms = require('./manis.utils');
var X2JS = require('x2js');
var adapter = require('webrtc-adapter');

var x2js = new X2JS({
    attributePrefix: ""
});
var _ssrcList = new Map();


/**
 * audio/video/data集合
 */
var mediaObjList = new Array();

/**
 * 1.保存VB节点，source-add时重用公共部分
 * 2.SDP对应的->cname : mixedaudio mixedvideo
 */
var _mixedAudioObj, _mixedVideoObj;

/**
 * 1.生成假的SSRC
 * 2.自增
 */
var _ssrc = 100;

var midMum = 0;

var SsrcObj = function (isNew) {
    this.jid = "";
    this.isNew = isNew;
};

var MediaObj = function (type) {
    this.type = type;
    this.ssrc = "";
    this.title = "";
    if (ms.getSemantics() != "plan-b") {
        this.mid = midMum;
    } else {
        this.mid = type;
    }
    this.body = "";
    if (this.type == "data") {
        this.ssrc = "0";
    }

    /**
     * 删除替换时保留原始MediaStream对象，根据endpoint修改
     */
    this.streamId = "";

    /**
     * sdp:mslabel对应MediaStream对象的ID属性
     */
    this.mslabel = "";

    midMum++;
};
var SdpUtil = {
    markScreenSSRC: function (xml) {
        var content = $(xml).find(">content");
        var videoNode = $(content).find('description[media="video"]') || null;
        var videoSources = videoNode.find('source[xmlns="urn:xmpp:jingle:apps:rtp:ssma:0"]') || null;
        for (var i = 0; i < videoSources.length; i++) {
            var videoSource = $(videoSources[i]);
            var isScreen = videoSource.attr('screen') || false;
            var videoSSRC = videoSource.attr('ssrc') || null;
            if (isScreen.toString() == 'true') {
                console.log('find screen source: ', videoSource[0], videoSSRC);
                window.screenSSRC = parseInt(videoSSRC);
            }
        }
    },
    xml2sdp: function (xml) {
        var that = this;
        that.markScreenSSRC(xml)
        var jsonObj = x2js.xml2js(xml);
        if (jsonObj && jsonObj.jingle) {
            var action = jsonObj.jingle.action;
            var contents = jsonObj.jingle.content;
            if (action && contents) {
                if (!contents.length) {
                    var _array = new Array();
                    _array.push(contents);
                    contents = _array;
                }
                if (action == "session-initiate") {
                    _ssrcList = new Map();
                    mediaObjList = new Array();

                    contents.forEach(function (content) {
                        var name = content.name;
                        if (name && content.description) {
                            if (name != "data") {
                                var sources = content.description.source;
                                if (!sources.length) {
                                    var _array = new Array();
                                    _array.push(sources);
                                    sources = _array;
                                }
                                sources.forEach(function (source) {
                                    that.getSessionInitiateMediaObj(name, content, source);
                                });
                            } else {
                                if (content.transport) {
                                    var title = "m=" + content.description.media + " 9 DTLS/SCTP " + content.transport.sctpmap.number + "\r\n"
                                        + "c=IN IP4 0.0.0.0" + "\r\n"
                                        + "a=ice-ufrag:" + content.transport.ufrag + "\r\n"
                                        + "a=ice-pwd:" + content.transport.pwd + "\r\n"
                                        + "a=ice-options:trickle" + "\r\n"
                                        + "a=fingerprint:" + content.transport.fingerprint.hash + " " + content.transport.fingerprint["__text"] + "\r\n"
                                        + "a=setup:actpass" + "\r\n"
                                    var candidates = content.transport.candidate;
                                    for (var i = 0; i < candidates.length; i++) {
                                        var candidate = candidates[i];
                                        title += "a=candidate:" + candidate.foundation
                                            + " " + candidate.component
                                            + " " + candidate.protocol
                                            + " " + candidate.priority
                                            + " " + candidate.ip
                                            + " " + candidate.port
                                            + " typ " + candidate.type + " generation " + candidate.generation + "\r\n";
                                    }
                                    title += "a=sendrecv" + "\r\n"
                                        + "a=rtcp-mux" + "\r\n";
                                    var body = "a=sctpmap:" + content.transport.sctpmap.number + " " + content.transport.sctpmap.protocol + " " + content.transport.sctpmap.streams + "\r\n";
                                    var media = new MediaObj(name);
                                    media.title = title;
                                    media.body = body;
                                    // 保存 data/application
                                    mediaObjList.push(media);
                                }
                            }
                        }
                    });

                } else if (action == "source-add") {
                    var that = this;
                    contents.forEach(function (content) {
                        that.getSourceAddObj(content);
                    });
                } else if (action == "source-remove") {
                    var that = this;
                    contents.forEach(function (content) {
                        that.getSourceRemoveObj(content);
                    });
                }

                var sdp = this.getOfferSDP();
                // console.log("========================sdp========" + action + "\n" + sdp + ",ssrcList:", _ssrcList);
                console.log("========================sdp========" + action + ",ssrcList:", _ssrcList);
                return sdp;
            }
        }
    },
    getSessionInitiateMediaObj: function (name, content, source) {
        if (source && name && content) {
            var streamId = ms.findValueByName(source.parameter, 'mslabel');
            if (streamId) {
                var media = new MediaObj(name);
                media.ssrc = source.ssrc;
                media.title = this.getMediaTtileSDP(name, content);
                media.body = this.getMediaBodySDP(source);

                media.mslabel = streamId;
                media.label = ms.findValueByName(source.parameter, 'label');
                media.streamId = streamId;

                this.saveSsrc(streamId, media, source, name, true);

                // 保存 audio / video
                mediaObjList.push(media);

                if (media.mslabel.indexOf("mixed") > -1) {
                    if (name == "audio") {
                        _mixedAudioObj = media;
                    } else {
                        _mixedVideoObj = media;
                    }
                }
            }
        }
    },
    getMediaBodySDP: function (source) {
        var rs = "";
        if (ms.getSemantics() != "plan-b") {
            rs += "a=msid:" + ms.findValueByName(source.parameter, 'msid') + "\r\n";
        }
        rs += "a=ssrc:" + source.ssrc + " cname:" + ms.findValueByName(source.parameter, 'cname') + "\r\n";
        if (ms.getSemantics() != "firefox") {
            rs += "a=ssrc:" + source.ssrc + " msid:" + ms.findValueByName(source.parameter, 'msid') + "\r\n"
                + "a=ssrc:" + source.ssrc + " mslabel:" + ms.findValueByName(source.parameter, 'mslabel') + "\r\n"
                + "a=ssrc:" + source.ssrc + " label:" + ms.findValueByName(source.parameter, 'label') + "\r\n";
        }
        return rs;
    },
    getMediaTtileSDP: function (name, content) {
        var payloads = content.description["payload-type"];
        if (!payloads.length) {
            var _array = new Array();
            _array.push(payloads);
            payloads = _array;
        }
        var payloadTypeIds = new Array();
        payloads.forEach(function (payload) {
            payloadTypeIds.push(payload.id);
        });

        var rs = "m=" + name + " 9 UDP/TLS/RTP/SAVPF " + payloadTypeIds.join(" ") + "\r\n"
            + "c=IN IP4 0.0.0.0" + "\r\n"
            + "a=rtcp:9 IN IP4 0.0.0.0\r\n"
            + "a=ice-ufrag:" + content.transport.ufrag + "\r\n"
            + "a=ice-pwd:" + content.transport.pwd + "\r\n"
            + "a=ice-options:trickle" + "\r\n"
            + "a=fingerprint:" + content.transport.fingerprint.hash + " " + content.transport.fingerprint["__text"] + "\r\n"
            + "a=setup:actpass" + "\r\n";
        if (name == "audio") {
            rs += "a=extmap:1 urn:ietf:params:rtp-hdrext:ssrc-audio-level" + "\r\n";
            // rs += "a=extmap:2 http://www.ietf.org/id/draft-holmer-rmcat-transport-wide-cc-extensions-01" + "\r\n";
            // rs += "a=extmap:3 urn:ietf:params:rtp-hdrext:sdes:mid" + "\r\n";
            // rs += "a=extmap:4 urn:ietf:params:rtp-hdrext:sdes:rtp-stream-id" + "\r\n";
            // rs += "a=extmap:5 urn:ietf:params:rtp-hdrext:sdes:repaired-rtp-stream-id" + "\r\n";
            // + "a=extmap:9 urn:ietf:params:rtp-hdrext:sdes:mid" + "\r\n"
        } else if (name == "video") {
            // rs += "a=extmap:3 http://www.webrtc.org/experiments/rtp-hdrext/abs-send-time" + "\r\n";
            //     + "a=extmap:4 urn:ietf:params:rtp-hdrext:sdes:rtp-stream-id" + "\r\n"
            //     + "a=extmap:5 http://www.ietf.org/id/draft-holmer-rmcat-transport-wide-cc-extensions-01" + "\r\n"
            //+ "a=extmap:9 urn:ietf:params:rtp-hdrext:sdes:mid" + "\r\n"

            rs += "a=extmap:3 http://www.webrtc.org/experiments/rtp-hdrext/abs-send-time" + "\r\n";
            var version = adapter.browserDetails.version;
            if (version > 63) {
                rs += "a=extmap:5 http://www.webrtc.org/experiments/rtp-hdrext/transport-wide-cc-02" + "\r\n";
            } else {
                rs += "a=extmap:5 http://www.ietf.org/id/draft-holmer-rmcat-transport-wide-cc-extensions-01" + "\r\n";
            }
        }
        // var rtpHdrext = content.description["rtp-hdrext"];
        // if (!rtpHdrext.length) {
        //     rs += "a=extmap:" + rtpHdrext.id + " " + rtpHdrext.uri + "\r\n";
        // } else {
        //     rtpHdrext.forEach(function (obj) {
        //         rs += "a=extmap:" + obj.id + " " + obj.uri + "\r\n";
        //     });
        // }
        var candidates = content.transport.candidate;
        for (var i = 0; i < candidates.length; i++) {
            var candidate = candidates[i];
            rs += "a=candidate:" + candidate.foundation
                + " " + candidate.component
                + " " + candidate.protocol
                + " " + candidate.priority
                + " " + candidate.ip
                + " " + candidate.port
                + " typ " + candidate.type + " generation " + candidate.generation + "\r\n";
        }
        rs += "a=sendrecv" + "\r\n"
            + "a=rtcp-mux" + "\r\n";

        payloads.forEach(function (payload) {
            var id = payload.id;
            var payloadName = payload.name;
            var clockrate = payload.clockrate;
            if (name == "audio") {
                var channels = payload.channels;
                if (channels) {
                    rs += "a=rtpmap:" + id + " " + payloadName + "/" + clockrate + "/" + channels + "\r\n";
                } else {
                    rs += "a=rtpmap:" + id + " " + payloadName + "/" + clockrate + "\r\n"
                }
                rs += "a=rtcp-fb:" + id + " transport-cc" + "\r\n";
            } else if (name == "video") {
                rs += "a=rtpmap:" + id + " " + payloadName + "/" + clockrate + "\r\n"
                if (payload["rtcp-fb"]) {
                    rs += "a=rtcp-fb:" + id + " ccm fir" + "\r\n"
                        + "a=rtcp-fb:" + id + " nack" + "\r\n"
                        + "a=rtcp-fb:" + id + " nack pli" + "\r\n"
                        + "a=rtcp-fb:" + id + " transport-cc" + "\r\n"
                }
            }
            if (payload.parameter) {
                if (payload.parameter.length) {
                    var pars = "";
                    for (var i = 0, j = payload.parameter.length; i < j; i++) {
                        if (i == 0) {
                            pars += payload.parameter[i]["name"] + "=" + payload.parameter[i]["value"];
                        } else {
                            pars += ";" + payload.parameter[i]["name"] + "=" + payload.parameter[i]["value"];
                        }
                    }
                    rs += "a=fmtp:" + id + " " + pars + "\r\n";
                } else {
                    rs += "a=fmtp:" + id + " " + payload.parameter["name"] + "=" + payload.parameter["value"] + "\r\n";
                }
            }
        });
        return rs;
    },
    getSourceAddObj: function (content) {
        var that = this;
        var name = content.name;
        if (name != "data") {
            if (ms.getSemantics() == "plan-b") {
                var sources = content.description.source;
                if (sources) {
                    if (!sources.length) {
                        var _array = new Array();
                        _array.push(sources);
                        sources = _array;
                    }
                    sources.forEach(function (source) {
                        var obj = new MediaObj(name);
                        if (name == "audio") {
                            obj.title = _mixedAudioObj.title;
                        } else {
                            obj.title = _mixedVideoObj.title;
                        }
                        var streamId = ms.findValueByName(source.parameter, 'mslabel');

                        if (streamId) {
                            obj.body = that.getMediaBodySDP(source);
                            obj.ssrc = source.ssrc;
                            obj.mslabel = streamId;
                            obj.label = ms.findValueByName(source.parameter, 'label');
                            obj.streamId = streamId;
                            mediaObjList.push(obj);

                            that.saveSsrc(streamId, obj, source, name, true);
                        }
                    });
                }
            } else {
                var flag = true;
                mediaObjList.forEach(function (obj) {
                    if (obj.type == name && obj.mslabel == "re_mslabel") {
                        var source = content.description.source;
                        if (source && flag) {
                            var mslabel = ms.findValueByName(source.parameter, 'mslabel');
                            if (mslabel) {
                                obj.body = that.getMediaBodySDP(source);
                                obj.mslabel = mslabel;
                                obj.label = ms.findValueByName(source.parameter, 'label');
                                obj.ssrc = source.ssrc;

                                that.saveSsrc(mslabel, obj, source, name, true);
                                flag = false;
                                return;
                            }
                        }
                    }
                });

                if (flag) {
                    var source = content.description.source;
                    var streamId = ms.findValueByName(source.parameter, 'mslabel');
                    if (source && streamId) {
                        var obj = new MediaObj(name);
                        if (name == "audio") {
                            obj.title = _mixedAudioObj.title;
                        } else {
                            obj.title = _mixedVideoObj.title;
                        }

                        obj.body = that.getMediaBodySDP(source);
                        obj.mslabel = streamId;
                        obj.label = ms.findValueByName(source.parameter, 'label');
                        obj.ssrc = source.ssrc;
                        obj.streamId = streamId;
                        mediaObjList.push(obj);

                        that.saveSsrc(streamId, obj, source, name, false);
                    }
                }
            }

        }
    },
    saveSsrc: function (streamId, obj, source, name, flag) {
        if (streamId && streamId.indexOf("mixed") == -1) {
            var type = {
                ssrc: obj.ssrc,
                label: obj.label
            };
            var ssrcObj = _ssrcList.get(streamId);
            if (!ssrcObj) {
                ssrcObj = new SsrcObj(flag);
                ssrcObj.jid = source.jid;
                ssrcObj[name] = type;
                _ssrcList.set(streamId, ssrcObj);
            } else {
                ssrcObj[name] = type;
            }
        }
    },
    getSourceRemoveObj: function (content) {
        var that = this;
        var name = content.name;
        if (name != "data") {
            var sources = content.description.source;
            if (sources) {
                if (!sources.length) {
                    var _array = new Array();
                    _array.push(sources);
                    sources = _array;
                }
                if (ms.getSemantics() == "plan-b") {
                    sources.forEach(function (source) {
                        var streamId = ms.findValueByName(source.parameter, 'mslabel');
                        if (streamId) {
                            _ssrcList.delete(streamId);
                            var _array = new Array();
                            mediaObjList.forEach(function (obj) {
                                if (obj.ssrc == source.ssrc) {
                                    $(document).trigger('mains.on.stream.removed', [name, source.ssrc, false]);
                                } else {
                                    _array.push(obj);
                                }
                            });
                            mediaObjList = _array;
                        }
                    });
                } else if (ms.getSemantics() == "unified-plan") {
                    sources.forEach(function (source) {
                        var streamId = ms.findValueByName(source.parameter, 'mslabel')
                        if (streamId) {
                            _ssrcList.delete(streamId);
                            mediaObjList.forEach(function (obj) {
                                if (obj.ssrc == source.ssrc) {
                                    obj.body = that.getReplaceMediaBodySDP();
                                    obj.ssrc = _ssrc;
                                    obj.label = "re_label";
                                    obj.mslabel = "re_mslabel";
                                    $(document).trigger('mains.on.stream.removed', [name, source.ssrc, false]);
                                }
                            });
                        }
                    });
                } else if (ms.getSemantics() == "firefox") {
                    sources.forEach(function (source) {
                        var msid = ms.findValueByName(source.parameter, 'msid');
                        if (msid) {
                            var labels = msid.split(" ");
                            var streamId = labels[0];
                            _ssrcList.delete(streamId);

                            mediaObjList.forEach(function (obj) {
                                if (obj.ssrc == source.ssrc) {
                                    obj.body = that.getReplaceMediaBodySDP();
                                    obj.ssrc = _ssrc;
                                    obj.label = "re_label";
                                    obj.mslabel = "re_mslabel";

                                    $(document).trigger('mains.on.stream.removed', [name, source.ssrc, false]);
                                }
                            });
                        }
                    });
                }
            }
        }
    },
    getOfferSDP: function () {
        if (ms.getSemantics() == "plan-b") {
            var bundleNumStr = "", audioGroup = "", videoGroup = "", dataGroup = "";
            mediaObjList.forEach(function (obj) {
                if (obj.type == "audio") {
                    if (audioGroup == "") {
                        bundleNumStr += " " + obj.mid;
                        audioGroup += obj.title;
                        audioGroup += "a=mid:" + obj.mid + "\r\n";
                        audioGroup += "b=AS:" + ms.getBandwidth(obj.type) + "\r\n";
                        audioGroup += obj.body;
                    } else {
                        audioGroup += obj.body;
                    }
                }
                if (obj.type == "video") {
                    if (videoGroup == "") {
                        bundleNumStr += " " + obj.mid;
                        videoGroup += obj.title;
                        videoGroup += "a=mid:" + obj.mid + "\r\n";
                        videoGroup += "b=AS:" + ms.getBandwidth(obj.type) + "\r\n";
                        videoGroup += obj.body;
                    } else {
                        videoGroup += obj.body;
                    }
                }
                if (obj.type == "data") {
                    if (dataGroup == "") {
                        bundleNumStr += " " + obj.mid;
                        dataGroup += obj.title;
                        dataGroup += "a=mid:" + obj.mid + "\r\n";
                        dataGroup += obj.body;
                    }
                }
            });
            var sdpOffer = "v=0\r\n"
                + "o=- 1923518516 2 IN IP4 0.0.0.0\r\n"
                + "s=-\r\n"
                + "t=0 0\r\n"
                + "a=group:BUNDLE" + bundleNumStr + "\r\n"
                + "a=msid-semantic: WMS\r\n"
                + audioGroup
                + videoGroup
                + dataGroup;
            return sdpOffer;
        } else {
            var bundleNumStr = "", bootSdpOffer = "";
            mediaObjList.forEach(function (obj) {
                bundleNumStr += " " + obj.mid;
                bootSdpOffer += obj.title;
                bootSdpOffer += "a=mid:" + obj.mid + "\r\n";
                if (obj.type != "data") {
                    bootSdpOffer += "b=AS:" + ms.getBandwidth(obj.type) + "\r\n";
                }
                bootSdpOffer += obj.body;
            });

            var sdpOffer = "v=0\r\n" +
                "o=- 1923518516 2 IN IP4 0.0.0.0\r\n" +
                "s=-\r\n" +
                "t=0 0\r\n" +
                "a=group:BUNDLE" + bundleNumStr + "\r\n" +
                "a=msid-semantic: WMS *\r\n" + bootSdpOffer;
            return sdpOffer;
        }
    },
    getSsrc: function (streamId) {
        if (streamId) {
            return _ssrcList.get(streamId);
        } else {
            return _ssrcList;
        }
    },
    getReplaceMediaBodySDP: function () {
        var rs = "a=msid:re_mslabel re_label\r\n"
            + "a=ssrc:" + _ssrc + " cname:re_cname\r\n"
        if (ms.getSemantics() != "firefox") {
            rs += "a=ssrc:" + _ssrc + " msid:re_mslabel re_label\r\n"
                + "a=ssrc:" + _ssrc + " mslabel:re_mslabel\r\n"
                + "a=ssrc:" + _ssrc + " label:re_label\r\n";
        }
        _ssrc++;
        return rs;
    }
};
exports = module.exports = SdpUtil;



