var SDPUtil = require('./strophe.jingle.sdp.util');
var adapter = require('webrtc-adapter');

var _uniqueId = Math.round(Math.random() * 10000);

var ms = {

    //is browser support bandwidth on the fly
    iSupBwOnFly: function() {
        var browser = adapter.browserDetails.browser;
        if (browser == "chrome" &&
            'RTCRtpSender' in window &&
            'setParameters' in window.RTCRtpSender.prototype){
            return true
        }  else {
            return false
        }
    },
    getVideoBandwidthByResolution: function(resolution){
        resolution = resolution.toString();
        var videoBandwidth = null;
        var rbwc = config.rbwc;
        if(!rbwc || !rbwc.length){
            return videoBandwidth;
        }
        for(var i = 0; i < rbwc.length; i++){
            var item = rbwc[i];
            if(item.height == resolution){
                videoBandwidth = item.bandwidth;
            }
        }
        return videoBandwidth;
    },

    // 针对不协商设置带宽因为设置了b=AS导致无效,故支持不协商设置带宽的浏览器版本,将b=AS设置为后台配置最大带宽
    getVideoMaxBandwidth: function() {
        var that = this,
            maxBandwidth = null;
        if (that.iSupBwOnFly()) {
            maxBandwidth = that.getVideoBandwidthByResolution(config.resolution)
        }
        return maxBandwidth || (config.videoBandwidth ? config.videoBandwidth : 256)// 256kbits (both min-max)
    },


    findValueByName: function (arr, name) {
        for (var i = 0; i < arr.length; i++) {
            if (arr[i].name == name) {
                return arr[i]["value"];
            }
        }
    },
    getUniqueId: function (suffix) {
        if (typeof (suffix) == "string" || typeof (suffix) == "number") {
            return ++_uniqueId + ":" + suffix;
        } else {
            return ++_uniqueId + "";
        }
    },
    textToXML: function (text) {
        var doc = null;
        if (window['DOMParser']) {
            var parser = new DOMParser();
            doc = parser.parseFromString(text, 'text/xml');
        } else if (window['ActiveXObject']) {
            var doc = new ActiveXObject("MSXML2.DOMDocument");
            doc.async = false;
            doc.loadXML(text);
        } else {
            throw Error('No DOMParser object found.');
        }
        return doc.firstChild;
    },

    ansToSessAcc: function (sess, contents) {
        var sessionAccept = '<iq to="' + sess.peerjid + '" type="set" xmlns="jabber:client" from="' + sess.connection.jid + '" id="' + this.getUniqueId("sendIQ") + '">' +
            '<jingle xmlns="urn:xmpp:jingle:1" action="session-accept" initiator="' + sess.initiator + '" responder="' + sess.responder + '" sid="' + sess.sid + '">' +
            '<attend xmlns="http://jitsi.org/protocol/colibri" type="singlevoice" endpoint=""/>' +
            '<group xmlns="urn:xmpp:jingle:apps:grouping:0" semantics="BUNDLE">' +
            '<content name="audio"/>' +
            '<content name="video"/>' +
            '<content name="data"/>' +
            '</group>' +
            contents +
            '</jingle></iq>';
        return this.textToXML(sessionAccept);
    },

    createDataNode: function (ufrag, pwd, fingerprints) {
        var contentNode =
            '<content creator="responder" name="data">' +
            '<transport ufrag="' + ufrag + '" pwd="' + pwd + '" xmlns="urn:xmpp:jingle:transports:ice-udp:1">' +
            '<sctpmap xmlns="urn:xmpp:jingle:transports:dtls-sctp:1" number="5000" protocol="webrtc-datachannel" streams="1024"/>' +
            '<fingerprint hash="' + fingerprints[0].split(":")[1] + '" xmlns="urn:xmpp:jingle:apps:dtls:0" setup="active">' + fingerprints[1] + '</fingerprint>' +
            '</transport>' +
            '</content>';
        return contentNode;
    },

    createMediaNode: function (mediaName, mediaGroup, ssrc, cname, msid, ufrag, pwd, fingerprints) {
        var contentNode =
            '<content creator="responder" name="' + mediaName + '" senders="both">' +
            '<description xmlns="urn:xmpp:jingle:apps:rtp:1" media="' + mediaName + '" ssrc="' + ssrc + '">';
        contentNode += this.createPayloadNode(mediaName, mediaGroup);
        contentNode += this.createSourceNode(mediaName, ssrc, cname, msid);
        contentNode += '</description>' +
            '<transport ufrag="' + ufrag + '" pwd="' + pwd + '" xmlns="urn:xmpp:jingle:transports:ice-udp:1">' +
            '<fingerprint hash="' + fingerprints[0].split(":")[1] + '" xmlns="urn:xmpp:jingle:apps:dtls:0" setup="active">' + fingerprints[1] + '</fingerprint>' +
            '</transport>' +
            '</content>';
        return contentNode;

    },

    createSourceNode: function (mediaName, ssrc, cname, msid) {
        var contentNode = '';
        contentNode += this.createSourceParamsNode(ssrc, cname, msid);
        contentNode += '<rtcp-mux/>';
        if (mediaName == "audio") {
            contentNode += '<rtp-hdrext xmlns="urn:xmpp:jingle:apps:rtp:rtp-hdrext:0" uri="urn:ietf:params:rtp-hdrext:ssrc-audio-level" id="1"/>';
        } else {
            contentNode +=
                '<rtp-hdrext xmlns="urn:xmpp:jingle:apps:rtp:rtp-hdrext:0" uri="http://www.webrtc.org/experiments/rtp-hdrext/abs-send-time" id="3"/>' +
                '<rtp-hdrext xmlns="urn:xmpp:jingle:apps:rtp:rtp-hdrext:0" uri="http://www.ietf.org/id/draft-holmer-rmcat-transport-wide-cc-extensions-01" id="5"/>';
        }
        return contentNode;
    },

    createPayloadNode: function (mediaName, mediaGroup) {
        var contentNode = '';
        var rtpmap = SDPUtil.find_line(mediaGroup, "a=rtpmap").split(":")[1];
        var rtpmap1 = rtpmap.split(" ")[1];
        if (mediaName == "audio") {
            var _fmtp = SDPUtil.find_line(mediaGroup, "a=fmtp"); //;
            if (_fmtp) {
                var fmtpMus = _fmtp.split(" ")[1].split(";");
                contentNode +=
                    '<payload-type id="' + rtpmap.split(" ")[0] + '" name="' + rtpmap1.split("/")[0] + '" clockrate="' + rtpmap1.split("/")[1] + '" channels="' + rtpmap1.split("/")[2] + '">' +
                    '<parameter name="minptime" value="' + fmtpMus[0].split("=")[1] + '"/>' +
                    '<parameter name="useinbandfec" value="' + fmtpMus[1].split("=")[1] + '"/>' +
                    '</payload-type>';
            } else {
                contentNode +=
                    '<payload-type id="' + rtpmap.split(" ")[0] + '" name="' + rtpmap1.split("/")[0] + '" clockrate="' + rtpmap1.split("/")[1] + '" channels="' + rtpmap1.split("/")[2] + '">' +
                    '</payload-type>';
            }
        } else {
            contentNode +=
                '<payload-type id="' + rtpmap.split(" ")[0] + '" name="' + rtpmap1.split("/")[0] + '" clockrate="' + rtpmap1.split("/")[1] + '" channels="1">' +
                '<rtcp-fb xmlns="urn:xmpp:jingle:apps:rtp:rtcp-fb:0" type="transport-cc"/>' +
                '<rtcp-fb xmlns="urn:xmpp:jingle:apps:rtp:rtcp-fb:0" type="ccm" subtype="fir"/>' +
                '<rtcp-fb xmlns="urn:xmpp:jingle:apps:rtp:rtcp-fb:0" type="nack"/>' +
                '<rtcp-fb xmlns="urn:xmpp:jingle:apps:rtp:rtcp-fb:0" type="nack" subtype="pli"/>' +
                '</payload-type>';
        }
        return contentNode;
    },
    getSemantics: function () {
        var semantics = null;
        var browser = adapter.browserDetails.browser;
        var version = adapter.browserDetails.version;
        if (browser == "chrome") {
            if (version > 82) {
                semantics = "unified-plan";
            } else {
                semantics = "plan-b";
            }
        } else if (browser == "firefox") {
            semantics = "firefox";
        } else {
            semantics = "plan-b";
        }
        return semantics;
    },
    createSourceParamsNode: function (ssrc, cname, msid) {
        var source = '<source ssrc="' + ssrc + '" xmlns="urn:xmpp:jingle:apps:rtp:ssma:0">' +
            '<parameter name="cname" value="' + cname + '"/>' +
            '<parameter name="msid" value="' + msid + '"/>' +
            '<parameter name="mslabel" value="' + msid.split(" ")[0] + '"/>' +
            '<parameter name="label" value="' + msid.split(" ")[1] + '"/>' +
            '</source>';
        return source;
    },

    getBandwidth: function (sourceType) {
        var that = this
        var bandwidth = {
            audio: config.audioBandwidth ? config.audioBandwidth : 64,   // 64kbits  minimum
            // video: config.videoBandwidth ? config.videoBandwidth : 256// 256kbits (both min-max)
            video: that.getVideoMaxBandwidth()
        };
        var browser = adapter.browserDetails.browser;
        var version = adapter.browserDetails.version;
        if (browser == "chrome") {
            if (version == 57) {
                bandwidth.audio *= 1000;
                bandwidth.video *= 1000;
            }
        }
        return bandwidth[sourceType];
    },
    getBrowserName: function () {
        return adapter.browserDetails.browser;
    },
    getBrowserVersion: function () {
        return adapter.browserDetails.version;
    },
    getBrowserInfo: function () {
        return {
            name: adapter.browserDetails.browser,
            version: adapter.browserDetails.version
        };
    },
    isSafari: function () {
        return adapter.browserDetails.browser == "safari";
    },
    isChrome: function () {
        return adapter.browserDetails.browser == "chrome";
    },
    isFirefox: function () {
        return adapter.browserDetails.browser == "firefox";
    },
    getResolution: function () {
        return [
            {width: {exact: 4096}, height: {exact: 2160}, videoBandwidth: 512 * 8},
            {width: {exact: 1920}, height: {exact: 1080}, videoBandwidth: 512 * 4},
            {width: {exact: 1280}, height: {exact: 720}, videoBandwidth: 512 * 2},
            {width: {exact: 1024}, height: {exact: 768}, videoBandwidth: 512 * 2},
            {width: {exact: 960}, height: {exact: 540}, videoBandwidth: 512 * 1},
            {width: {exact: 640}, height: {exact: 480}, videoBandwidth: 512 * 1},
            // {width: {exact: 640}, height: {exact: 360}, videoBandwidth: 512 * 0.5},
            {width: {exact: 320}, height: {exact: 240}, videoBandwidth: 512 * 0.5},
            {width: {exact: 320}, height: {exact: 180}, videoBandwidth: 512 * 0.5}
        ];
    }
};
