TraceablePeerConnection.prototype.modifySources = function (successCallback, isNeedAnswer) {
  var self = this;
  if (this.signalingState == 'closed') return;
  if (!(this.addssrc.length || this.removessrc.length || this.pendingop !== null || this.switchstreams)) {
    // There is nothing to do since scheduled job might have been executed by another succeeding call
    console.warn('There is nothing to do since scheduled job might have been executed by another succeeding call');
    if (successCallback) {
      successCallback();
    }
    return;
  }

  // FIXME: this is a big hack
  // https://code.google.com/p/webrtc/issues/detail?id=2688
  // ^ has been fixed.

  // Reset switch streams flag
  this.switchstreams = false;
  console.log('222222222222222221111111111111111111 \n', this.remoteDescription.sdp);
  var sdp = new SDP(this.remoteDescription.sdp);
  console.log('22222222222222222222222222222222222 \n', sdp.raw);
  // add sources
  this.addssrc.forEach(function (lines, idx) {
    // sdp.media[idx] += lines;
    sdp.media.push(lines);
  });
  this.addssrc = [];

  // remove sources
  this.removessrc.forEach(function (lines, idx) {
    lines = lines.split('\r\n');
    lines.pop(); // remove empty last element;
    lines.forEach(function (line) {
      sdp.media[idx] = sdp.media[idx].replace(line + '\r\n', '');
    });
  });
  this.removessrc = [];

  // FIXME:
  // this was a hack for the situation when only one peer exists
  // in the conference.
  // check if still required and remove
  if (sdp.media[0])
    sdp.media[0] = sdp.media[0].replace('a=recvonly', 'a=sendrecv');
  if (sdp.media[1])
    sdp.media[1] = sdp.media[1].replace('a=recvonly', 'a=sendrecv');

  sdp.raw = sdp.session + sdp.media.join('');
  console.log('222222222222222223333333333333333333 \n', sdp.raw, "\n", sdp.media);
  this.setRemoteDescription(new RTCSessionDescription({type: 'offer', sdp: sdp.raw}),
    function () {

      if (self.signalingState == 'closed') {
        console.error("createAnswer attempt on closed state");
        return;
      }

      if (isNeedAnswer) {
        self.createAnswer(
          function (modifiedAnswer) {
            // change video direction, see https://github.com/jitsi/jitmeet/issues/41
            console.log('AAAAAAAAAAAAAAAAA new answer: ', modifiedAnswer);
            if (self.pendingop !== null) {
              var sdp = new SDP(modifiedAnswer.sdp);
              if (sdp.media.length > 1) {
                switch (self.pendingop) {
                  case 'mute':
                    sdp.media[1] = sdp.media[1].replace('a=sendrecv', 'a=recvonly');
                    break;
                  case 'unmute':
                    sdp.media[1] = sdp.media[1].replace('a=recvonly', 'a=sendrecv');
                    break;
                }
                sdp.raw = sdp.session + sdp.media.join('');
                modifiedAnswer.sdp = sdp.raw;
              }
              self.pendingop = null;
            }

            // FIXME: pushing down an answer while ice connection state
            // console.log('AAAAAAAAAAAAAAAA will set local description ', manualRemoveUnknownSSRCFromSDP(modifiedAnswer));
            // modifiedAnswer = manualRemoveUnknownSSRCFromSDP(modifiedAnswer);
            // is still checking is bad...
            //console.log(self.peerconnection.iceConnectionState);

            // trying to work around another chrome bug
            //modifiedAnswer.sdp = modifiedAnswer.sdp.replace(/a=setup:active/g, 'a=setup:actpass');
            self.setLocalDescription(modifiedAnswer,
              function () {
                //console.log('modified setLocalDescription ok');
                if (successCallback) {
                  successCallback();
                }
              },
              function (error) {
                console.error('modified setLocalDescription failed', error);
              }
            );
          },
          function (error) {
            console.error('modified answer failed', error);
          }
        );
      } else {
        // 不需要set local description ,直接解锁
        self.negotiationInprogress = false;
      }
    },
    function (error) {
      console.error('modify failed', error);
    }
  );
};

function manualRemoveUnknownSSRCFromSDP(beforeAnswer) {

  // console.log('AAAAAAAAAAAAAAAAAAAAAAAAAAA before modify media : ', transform.parse(beforeAnswer.sdp).media, ' \r Before answer : ', beforeAnswer);

  var sdp = transform.parse(beforeAnswer.sdp);
  var media = sdp.media;
  media.forEach(function (value, key) {
    if (value.type == 'video') {
      var video = value;
      var ssrcsLength = video.ssrcs.length;
      if (ssrcsLength > 4) { // 如果是屏幕分享者,则需要发送两股流.
        // rebuilt ssrcs
        var newSSRCS = [];
        newSSRCS.push(video.ssrcs[ssrcsLength - 4]);
        newSSRCS.push(video.ssrcs[ssrcsLength - 3]);
        newSSRCS.push(video.ssrcs[ssrcsLength - 2]);
        newSSRCS.push(video.ssrcs[ssrcsLength - 1]);
        video.ssrcs = newSSRCS;
      }
      media[key] = video;
    }
  });
  sdp.media = media;
  // console.log('AAAAAAAAAAAAAAAAAAAAAAAAAAA after modify media : ', media, 'after sdp : ', transform.write(sdp));
  beforeAnswer.sdp = transform.write(sdp);

  // console.log('AAAAAAAAAAAAAAAAAAAAAAAAAAA after modify media : ', ' \r after answer : ', beforeAnswer);
  return beforeAnswer;
}

TraceablePeerConnection.prototype.close = function () {
  this.trace('stop');
  if (this.statsinterval !== null) {
    window.clearInterval(this.statsinterval);
    this.statsinterval = null;
  }
  this.peerconnection.close();
};

